import CustomButton from "./CustomButton"
import DeathHourglassTimer from "./DeathHourglassTimer"
import Overlay, { CaptureOverlay } from "./Overlay"
import ModalWindow from "./ModalWindow"
import CustomInput from "./CustomInput"
import React, { useRef, useState } from "react"
import { useSelector, useDispatch } from "react-redux"
import { updateSetting } from "../store"
import "./DeathDayMeter.scss"

export default function DeathDayMeter({
  value,
  subValue,
  isRunning,
  onChange,
  onInc,
  onPlay,
  onPause,
  maxHeight,
  imageUrl,
}) {
  const ref = useRef()
  const handlerRef = useRef()
  const [showEditModal, setShowEditModal] = useState(false)
  const [editValue, setEditValue] = useState("")
  const dispatch = useDispatch()
  const settings = useSelector(state => state.system.settings)
  
  //    settings     
  const buttonsText = settings?.globalTimerAddButtons || "+10 +5 +1 | -1 -5 -10"
  
  //    
  const parseButtons = (text) => {
    if (!text) return []
    //   
    return text.split(/\s+/).map(s => s.trim()).filter(s => s).map(s => parseInt(s))
  }
  
  const buttonValues = parseButtons(buttonsText)

  //  value ()      DeathHourglassTimer
  // value   
  const totalMinutes = value || 0
  const minutes = Math.trunc(totalMinutes)
  // subValue   (    60000)
  //    (0-59)
  let seconds = 0
  if (subValue) {
    const milliseconds = parseInt(subValue) || 0
    seconds = Math.floor(milliseconds / 1000) % 60
  }

  const [editButtonsText, setEditButtonsText] = useState(buttonsText)
  
  // Evaluate formula (supports = prefix like Excel, but not required)
  function evaluateFormula(formula) {
    if (!formula || formula.trim() === '') {
      return 0
    }
    
    let expression = formula.trim()
    
    // Remove = prefix if present (Excel-style)
    if (expression.startsWith('=')) {
      expression = expression.substring(1).trim()
    }
    
    // Only allow safe characters: numbers, operators, parentheses, spaces, decimal point
    // This prevents code injection
    const safePattern = /^[0-9+\-*/().\s]+$/
    if (!safePattern.test(expression)) {
      return NaN
    }
    
    try {
      // Evaluate the expression safely
      // Using Function constructor is safer than eval as it doesn't have access to local scope
      const result = new Function('return ' + expression)()
      
      // Convert to number and round to nearest integer (minutes)
      const numResult = Number(result)
      if (isNaN(numResult) || !isFinite(numResult)) {
        return NaN
      }
      
      return Math.round(numResult)
    } catch (error) {
      return NaN
    }
  }
  
  function handleEditClick() {
    setEditValue(String(value || 0))
    setEditButtonsText(buttonsText)
    setShowEditModal(true)
  }

  function handleEditOK() {
    const evaluatedValue = evaluateFormula(editValue)
    const newValue = isNaN(evaluatedValue) ? parseInt(editValue) || 0 : evaluatedValue
    onChange(newValue)
    dispatch(updateSetting({ key: "globalTimerAddButtons", value: editButtonsText }))
    setShowEditModal(false)
  }

  function handleEditCancel() {
    setShowEditModal(false)
  }

  let bb = ref.current ? ref.current.getBoundingClientRect() : { width: 0, height: 0 }
  let [dragStartPoint, setDragStartPoint] = useState({ x: 0, y: 0 })

  function handleOverlayDragStart(e) {
    let { x, y } = e.detail.startPoint
    const bb = handlerRef.current && handlerRef.current.getBoundingClientRect() || { x: 0, y: 0 }
    x = x - bb.x
    y = y - bb.y
    setDragStartPoint({ x, y })
    console.log("dragStartPoint", { x, y })
  }

  return (
    <div
      className="death-day-meter"
    >
      <div
      >
        <div
          ref={handlerRef}
          style={{
            position: "absolute",
            left: "50%",
            top: "15%",
            zIndex: 1000,
          }}
        >
          <Overlay
            minXOffset={0}
            minYOffset={0}
            onDragStart={bb => handleOverlayDragStart(bb)}
            base={
              <div
                style={{
                  width: bb.width / 2.5 + "px",
                  height: bb.height / 8 + "px",
                }}
              />
            }
            overlay={
              <div
                className="death-gauge-overlay"
                style={{
                  position: "absolute",
                  left: dragStartPoint.x - 20 + "px",
                  top: dragStartPoint.y - 20 + "px",
                }}
              >
                <div className="gauge-overlay-horizontal">
                  <CaptureOverlay>
                    <CustomButton
                      onClick={handleEditClick}
                      className="gauge-action gauge-action-edit"
                    >
                      EDIT
                    </CustomButton>
                  </CaptureOverlay>
                  <CaptureOverlay>
                    {isRunning && (
                      <CustomButton
                        onClick={() => onPause()}
                        className="gauge-action gauge-action-play"
                      >
                        PAUSE
                      </CustomButton>
                    )}
                    {!isRunning && (
                      <CustomButton
                        onClick={() => onPlay()}
                        className="gauge-action gauge-action-play"
                      >
                        PLAY
                      </CustomButton>
                    )}
                  </CaptureOverlay>
                </div>
                <div className="gauge-overlay-vertical" style={{ 
                  top: - buttonValues.length * 40 / 2 + 20 + "px",
                 }}>
                  {buttonValues.map((value, index) => {
                    if (value) {
                      return (
                        <CaptureOverlay key={index}>
                          <CustomButton
                            onClick={() => onInc(value)}
                            className="gauge-action"
                          >
                            {value > 0 ? `+${value}` : String(value)}
                          </CustomButton>
                        </CaptureOverlay>
                      )
                    } else {
                      return <div key={index} className="gauge-input-sep" />
                    }
                  })}
                </div>
              </div>
            }
          />
        </div>


        <div ref={ref}>
          <DeathHourglassTimer
            minutes={minutes}
            seconds={seconds}
            imageUrl={imageUrl}
          />
        </div>
      </div>

      {showEditModal && (
        <ModalWindow>
          <div>
            <label>Value (minutes) or formula (e.g., "10+5", "=20*2"):</label>
            <CustomInput
              type="text"
              value={editValue}
              onChange={e => setEditValue(e.target.value)}
              style={{
                marginTop: "10px",
                marginBottom: "10px",
                width: "100%",
                padding: "8px",
              }}
              autoFocus
            />
            <label style={{ marginTop: "20px", display: "block" }}>
              Timer buttons (separated by |, x, s or any letter):
            </label>
            <textarea
              value={editButtonsText}
              onChange={e => setEditButtonsText(e.target.value)}
              style={{
                marginTop: "10px",
                marginBottom: "10px",
                width: "100%",
                padding: "8px",
                minHeight: "60px",
                fontFamily: "monospace",
              }}
            />
            <div style={{ marginTop: "20px" }}>
              <CustomButton onClick={handleEditOK} style={{ marginRight: "10px" }}>
                OK
              </CustomButton>
              <CustomButton onClick={handleEditCancel}>
                Cancel
              </CustomButton>
            </div>
          </div>
        </ModalWindow>
      )}
    </div>
  )
}

