import React from "react";

/**
 * Props:
 * - minutes: number (  )
 * - seconds: number (0..59,         )
 * - style: React.CSSProperties ()
 * - className: string ()
 * - imageUrl: string (,   "/death.png")
 */
export default function DeathHourglassTimer({
  minutes = 0,
  seconds = 0,
  style,
  className,
  imageUrl = process.env.PUBLIC_URL + "/death-black-gradient.png",
  ref,
}) {
  const isNegative = minutes < 0;
  const absMinutes = Math.abs(Math.trunc(minutes));

  const sec = Math.abs(Math.trunc(seconds));
  const clampedSec = Math.max(0, Math.min(59, sec));
  const sec2 = String(clampedSec).padStart(2, "0");

  return (
    <div
      className={className}
      style={{
        position: "relative",
        width: "100%",
        height: "100%",
        maxWidth: "512px",
        maxHeight: "768px",
        margin: "0 auto",
        aspectRatio: "512 / 768",
        userSelect: "none",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        backgroundColor: "transparent",
        ...style,
      }}
      aria-label="Death hourglass timer"
    >
      <img
        src={imageUrl}
        alt="Death hourglass"
        style={{
          width: "100%",
          height: "100%",
          objectFit: "contain",
          imageRendering: "pixelated",
          pointerEvents: "none",
        }}
      />
      {/*    ( top/left   ) */}
      <div
        style={{
          position: "absolute",
          left: "75%",
          top: "20%",
          transform: "translate(-50%, -50%)",
          display: "inline-flex",
          alignItems: "flex-end",
          gap: 6,

          fontFamily: '"Consolas", "Inconsolata", "Source Code Pro", "Fira Code", "JetBrains Mono", "Courier New", Monaco, monospace',
          color: isNegative ? "rgba(255,0,0,0.88)" : "rgba(235, 110, 0, 0.95)",
          textShadow:
            "0 2px 0 rgba(0,0,0,0.9), 0 0 10px rgba(255,140,0,0.35)",
          letterSpacing: "-0.02em",
          lineHeight: 1,
          pointerEvents: "none",
        }}
      >
        <span style={{ position: "relative", fontSize: 64 }}>
          {isNegative && (
            <span
              style={{
                position: "absolute",
                right: "100%",
                marginRight: "4px",
                fontSize: 54,
                transform: "translate(4px, 6px)",
                lineHeight: 1,
              }}
            >
              -
            </span>
          )}
          {absMinutes}
        </span>

        <span
          style={{
            fontSize: 10,
            opacity: 0.95,
            transform: "translate(-8px, -11px)",
            letterSpacing: "0.02em",
          }}
        >
          .{sec2}
        </span>
      </div>
    </div>
  );
}

