import React, { useState, useRef, useEffect } from "react"
import { Sortable, SortableItem, SortableDragHandler } from "./Sortable"
import { Swipeable, SwipeableRightAction, SwipeableLeftAction } from "./Swipeable"
import { ContextMenu, ContextMenuItem, ContextMenuTarget } from "./ContextMenu"
import "./Checklist.scss"

function ChecklistItem({
  item,
  onToggle,
  onTextChange,
  onDelete,
  itemRef: externalItemRef
}) {
  const [isEditing, setIsEditing] = useState(false)
  const [localText, setLocalText] = useState(item.text)
  const inputRef = useRef(null)
  const internalItemRef = useRef(null)
  const itemRef = externalItemRef || internalItemRef

  useEffect(() => {
    if (isEditing && inputRef.current) {
      inputRef.current.focus()
      inputRef.current.select()
    }
  }, [isEditing])

  useEffect(() => {
    setLocalText(item.text)
  }, [item.text])

  const handleBlur = () => {
    setIsEditing(false)
    if (localText.trim() === '') {
      onDelete()
    } else if (localText !== item.text) {
      onTextChange(localText)
    }
  }

  const handleKeyDown = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault()
      inputRef.current?.blur()
    } else if (e.key === 'Escape') {
      setLocalText(item.text)
      setIsEditing(false)
    }
  }

  return (
    <div className="checklist-item" ref={itemRef} data-checklist-item-id={item.id}>
      <SortableDragHandler>
        <div className="checklist-drag-handle">
          <span className="drag-dots"></span>
        </div>
      </SortableDragHandler>
      <label className="checklist-checkbox">
        <input
          type="checkbox"
          checked={!!item.checked}
          disabled={item.checked === null}
          onChange={(e) => onToggle(e, itemRef.current)}
        />
        <span className="checkmark"></span>
      </label>
      {isEditing ? (
        <input
          ref={inputRef}
          className="checklist-text-input"
          type="text"
          value={localText}
          onChange={(e) => setLocalText(e.target.value)}
          onBlur={handleBlur}
          onKeyDown={handleKeyDown}
        />
      ) : (
        <span
          className={`checklist-text ${item.checked !== false ? 'checked' : ''}`}
          onClick={() => setIsEditing(true)}
        >
          {item.text}
        </span>
      )}
    </div>
  )
}

function AddItemPlaceholder({ onClick, position }) {
  const [isEditing, setIsEditing] = useState(false)
  const [text, setText] = useState('')
  const inputRef = useRef(null)

  useEffect(() => {
    if (isEditing && inputRef.current) {
      inputRef.current.focus()
    }
  }, [isEditing])

  const handleClick = () => {
    setIsEditing(true)
  }

  const handleBlur = () => {
    if (text.trim()) {
      onClick(text.trim())
      setText('')
      // Stay in editing mode to allow adding more items quickly
      setTimeout(() => {
        if (inputRef.current) {
          inputRef.current.focus()
        }
      }, 10)
    } else {
      setText('')
      setIsEditing(false)
    }
  }

  const handleKeyDown = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault()
      if (text.trim()) {
        onClick(text.trim())
        setText('')
        // Keep editing mode to allow adding more items
        setTimeout(() => {
          if (inputRef.current) {
            inputRef.current.focus()
          }
        }, 10)
      }
    } else if (e.key === 'Escape') {
      setText('')
      setIsEditing(false)
    }
  }

  return (
    <div
      className={`checklist-placeholder ${position}`}
      onClick={!isEditing ? handleClick : undefined}
      onFocus={!isEditing ? handleClick : undefined}
      tabIndex={0}
    >
      <div className="checklist-item placeholder-item">
        <div className="checklist-drag-handle placeholder">
          <span className="drag-dots"></span>
        </div>
        <label className="checklist-checkbox placeholder">
          <span className="checkmark"></span>
        </label>
        {isEditing ? (
          <input
            ref={inputRef}
            className="checklist-text-input placeholder-input"
            type="text"
            value={text}
            placeholder="Add item..."
            onChange={(e) => setText(e.target.value)}
            onBlur={handleBlur}
            onKeyDown={handleKeyDown}
            onClick={(e) => e.stopPropagation()}
          />
        ) : (
          <span className="checklist-text placeholder-text">Add item...</span>
        )}
      </div>
    </div>
  )
}

export default function Checklist({
  items = [],
  onChange,
  expanded = false,
  onToggleExpand
}) {
  const itemRefs = useRef({})

  //      ref  
  const getItemRef = (itemId) => {
    if (!itemRefs.current[itemId]) {
      itemRefs.current[itemId] = React.createRef()
    }
    return itemRefs.current[itemId]
  }

  const handleAddItem = (text, position = 'bottom') => {
    const newItem = {
      id: Date.now().toString(),
      text,
      checked: false
    }

    if (position === 'top') {
      onChange([newItem, ...items], 0)
    } else {
      onChange([...items, newItem], 0)
    }
  }

  const handleToggle = (index, eventOrElement) => {
    const newItems = [...items]
    const oldChecked = newItems[index].checked
    //  : null -> false, false -> true, true -> false
    let newChecked
    if (oldChecked === null) {
      newChecked = false
    } else {
      newChecked = !oldChecked
    }
    newItems[index] = { ...newItems[index], checked: newChecked }
    const checkedDelta = (oldChecked ? -1 : (newChecked ? 1 : 0))

    //      
    let coordinates = null
    if (eventOrElement) {
      let element = null
      if (eventOrElement.currentTarget) {
        element = eventOrElement.currentTarget
      } else if (eventOrElement.target) {
        element = eventOrElement.target.closest('.checklist-item') || eventOrElement.target
      } else if (eventOrElement.getBoundingClientRect) {
        element = eventOrElement
      }

      if (element) {
        const rect = element.getBoundingClientRect()
        const container = document.getElementById('table-page') || document.body
        const containerRect = container.getBoundingClientRect()

        coordinates = {
          left: rect.left - containerRect.left + rect.width / 2,
          top: rect.top - containerRect.top + rect.height / 2
        }
      }
    }

    onChange(newItems, checkedDelta, coordinates)
  }

  const handleNullify = (index, isCrossOut = true) => {
    const newItems = [...items]
    const item = newItems[index]
    const wasChecked = !!item.checked
    //  isCrossOut=true, : checked = null ()
    //  isCrossOut=false,  : checked = false
    newItems[index] = { ...item, checked: isCrossOut ? null : false }
    //    checked,  checkedDelta
    const checkedDelta = wasChecked ? -1 : 0
    onChange(newItems, checkedDelta)
  }

  const handleTextChange = (index, text) => {
    const newItems = [...items]
    newItems[index] = { ...newItems[index], text }
    onChange(newItems, 0)
  }

  const handleDelete = (index) => {
    const deletedItem = items[index]
    const newItems = items.filter((_, i) => i !== index)
    const checkedDelta = deletedItem.checked ? -1 : 0
    onChange(newItems, checkedDelta)
  }

  const handleReorder = (fromIndex, toIndex) => {
    const newItems = [...items]
    const [removed] = newItems.splice(fromIndex, 1)
    newItems.splice(toIndex, 0, removed)
    onChange(newItems, 0)
  }

  const handleCheckAll = () => {
    const newItems = items.map(item => ({ ...item, checked: true }))
    const checkedDelta = items.filter(item => !item.checked).length
    onChange(newItems, checkedDelta)
  }

  const handleUncheckAll = () => {
    const newItems = items.map(item => ({ ...item, checked: false }))
    const checkedDelta = -items.filter(item => item.checked).length
    onChange(newItems, checkedDelta)
  }

  const handleDeleteAll = () => {
    onChange([])
  }

  const handleDeleteChecked = () => {
    const newItems = items.filter(item => !item.checked)
    onChange(newItems)
  }

  const checkedCount = items.filter(i => i.checked).length

  return (
    <div className={`checklist ${expanded ? 'expanded' : 'collapsed'}`}>
      {items.length > 0 && (
        <ContextMenu>
          <ContextMenuTarget>
            <div className="checklist-header" onClick={onToggleExpand}>
              <span className="checklist-toggle">{expanded ? '' : ''}</span>
              <span className="checklist-summary">
                {checkedCount}/{items.length} completed
              </span>
            </div>
          </ContextMenuTarget>
          <ContextMenuItem onClick={handleUncheckAll}>
            Uncheck all
          </ContextMenuItem>
          <ContextMenuItem onClick={handleCheckAll}>
            Check all
          </ContextMenuItem>
          <hr />
          <ContextMenuItem onClick={handleDeleteAll}>
            Delete all
          </ContextMenuItem>
          <ContextMenuItem onClick={handleDeleteChecked}>
            Delete checked
          </ContextMenuItem>
        </ContextMenu>
      )}

      {(expanded || items.length === 0) && (
        <div className="checklist-content">
          {items.length > 0 && (
            <AddItemPlaceholder
              onClick={(text) => handleAddItem(text, 'top')}
              position="top"
            />
          )}

          {items.length > 0 && (
            <Sortable onMove={handleReorder}>
              {items.map((item, index) => {
                const itemRef = getItemRef(item.id)
                return (
                  <SortableItem key={item.id}>
                    <Swipeable>
                      {


                        item.checked !== null && (
                          <SwipeableLeftAction width={120} onAction={() => handleNullify(index, true)}>
                            Cross out
                          </SwipeableLeftAction>
                        ) ||
                        <SwipeableLeftAction width={120} onAction={() => handleNullify(index, false)}>
                          Uncross
                        </SwipeableLeftAction>

                      }
                      <SwipeableLeftAction width={120} onAction={() => {
                        //      ref    
                        const element = itemRef.current ||
                          document.querySelector(`[data-checklist-item-id="${item.id}"]`) ||
                          document.querySelectorAll('.checklist-item')[index]
                        handleToggle(index, element)
                      }}>
                        {item.checked ? 'Uncheck' : 'Check'}
                      </SwipeableLeftAction>
                      <SwipeableRightAction width={120} onAction={() => handleDelete(index)}>
                        Delete
                      </SwipeableRightAction>
                      <ChecklistItem
                        item={item}
                        itemRef={itemRef}
                        onToggle={(e, element) => handleToggle(index, e || element || itemRef.current)}
                        onTextChange={(text) => handleTextChange(index, text)}
                        onDelete={() => handleDelete(index)}
                      />
                    </Swipeable>
                  </SortableItem>
                )
              })}
            </Sortable>
          )}

          <AddItemPlaceholder
            onClick={(text) => handleAddItem(text, items.length === 0 ? 'bottom' : 'bottom')}
            position="bottom"
          />
        </div>
      )}
    </div>
  )
}

