import React from 'react'
import { removeOnTimeout } from "../components/Timeout"
import './Reward.scss'

export default function Reward({ left, top, children, className, color }) {
  const style = {
    left: `${left}px`,
    top: `${top}px`,
  }
  if (color) {
    style.color = color;
  }
  return <div style={style} className={`reward ${className}`}>{children}</div>
}


export async function floatReward({
  left, top, value, container, timeout, className, color,
}) {
  removeOnTimeout(
    <Reward left={left} top={top} className={className} color={color}>
      {value}
    </Reward>,
    timeout,
    container,
  )
}