import { useDispatch, useSelector } from "react-redux"
import { updateAllTasks, updateSetting } from "../store"
import "./RewardControls.scss"

export default function SetAllRewards() {
  const dispatch = useDispatch()
  const settings = useSelector(state => state.system.settings) || {}
  
  const mode = settings.lastSetAllRewardMode ?? "auto"
  const value = settings.lastSetAllRewardValue ?? 0

  const handleApply = () => {
    // Save current values to settings
    dispatch(updateSetting({ key: "lastSetAllRewardMode", value: mode }))
    dispatch(updateSetting({ key: "lastSetAllRewardValue", value: value }))
    
    if (mode === "auto") {
      dispatch(updateAllTasks({ reward: null }))
    } else if (mode === "zero") {
      dispatch(updateAllTasks({ reward: 0 }))
    } else if (mode === "value") {
      const numValue = parseInt(value) || 0
      dispatch(updateAllTasks({ reward: numValue }))
    }
  }

  return (
    <div className="reward-controls-section">
      <div className="reward-controls-row">
        <span>Set All Rewards to</span>
        <select 
          value={mode}
          onChange={e => dispatch(updateSetting({ key: "lastSetAllRewardMode", value: e.target.value }))}
          className="reward-controls-select"
        >
          <option value="auto">auto</option>
          <option value="zero">zero</option>
          <option value="value">value</option>
        </select>
        {mode === "value" && (
          <input 
            type="number" 
            value={value}
            onChange={e => dispatch(updateSetting({ key: "lastSetAllRewardValue", value: parseInt(e.target.value) || 0 }))}
            className="reward-controls-input reward-controls-input--number"
          />
        )}
        <button 
          onClick={handleApply}
          className="reward-controls-button reward-controls-button--green"
        >
          Apply
        </button>
      </div>
    </div>
  )
}

