import { useDispatch, useSelector } from "react-redux"
import { updateTask, updateSetting } from "../store"
import { useTasks } from "../store/tasks"
import { useDays } from "../store/days"
import { useMeasures } from "../store/measures"
import { MEASURE_TYPE_EMPTY, MEASURE_TYPE_NUMBER } from "../models/Measure"
import { getFreq } from "../utils/taskUtils"
import "./RewardControls.scss"

export default function AdjustByLastWeek() {
  const dispatch = useDispatch()
  const tasks = useTasks()
  const days = useDays()
  const { getMeasureValueByTaskIdAndDayId } = useMeasures()
  const settings = useSelector(state => state.system.settings) || {}

  const rewardsText = settings.rewardsSequence ?? "1 10 20 50 100 150 500 1000"

  // Parse rewards from text (split by spaces, commas, or any whitespace)
  const parseRewards = (text) => {
    return text
      .split(/[\s,;]+/)
      .map(s => s.trim())
      .filter(s => s.length > 0)
      .map(s => parseFloat(s))
      .filter(n => !isNaN(n))
  }

  const rewards = parseRewards(rewardsText)

  // Get week frequency for a task (0 to 1)
  const getWeekFreq = (taskId) => {
    return getFreq(taskId, 7, tasks, days, getMeasureValueByTaskIdAndDayId)
  }

  const handleAdjustByLastWeek = () => {
    // Save current rewards sequence to settings
    dispatch(updateSetting({ key: "rewardsSequence", value: rewardsText }))

    if (rewards.length === 0) {
      alert("Please enter at least one reward value")
      return
    }

    tasks.forEach(task => {
      if (task.isGroup) return

      const weekFreq = getWeekFreq(task.id)
      const fractionalIndex = weekFreq * rewards.length
      const floorIndex = Math.floor(fractionalIndex)
      const i = Math.max(0, Math.min(rewards.length - 1, floorIndex))
      const k = fractionalIndex - floorIndex
      
      let newReward
      if (i + 1 < rewards.length) {
        // Interpolate between rewards[i] and rewards[i+1]
        newReward = rewards[i] + (rewards[i + 1] - rewards[i]) * k
      } else {
        // Use last reward if we're at the end
        newReward = rewards[rewards.length - 1]
      }

      let minReward = rewards[0]
      let maxReward = rewards[rewards.length - 1]
      newReward = Math.max(minReward, Math.min(maxReward, newReward))
      newReward = Math.round(newReward)

      dispatch(updateTask({ id: task.id, reward: newReward }))
    })
  }

  return (
    <div className="reward-controls-section">
      <h4 className="reward-controls-title">Adjust by Last Week</h4>
      <div className="reward-controls-row">
        <div className="reward-controls-field" style={{ width: "100%" }}>
          <label className="reward-controls-label">Rewards Sequence</label>
          <textarea
            value={rewardsText}
            onChange={e => dispatch(updateSetting({ key: "rewardsSequence", value: e.target.value }))}
            className="reward-controls-input"
            style={{
              width: "100%",
              minHeight: "80px",
              resize: "vertical",
              fontFamily: "monospace"
            }}
            placeholder="1 10 20 50 100 150 500 1000"
          />
        </div>
      </div>
      <div className="reward-controls-row">
        <button
          onClick={handleAdjustByLastWeek}
          className="reward-controls-button reward-controls-button--blue"
        >
          Apply
        </button>
      </div>
      <p className="reward-controls-hint">
        Parsed rewards: {rewards.length > 0 ? rewards.join(", ") : "none"} ({rewards.length} values)
        <br />
        Reward = rewards[i] + (rewards[i+1] - rewards[i])  k, where i = floor(weekFreq  {rewards.length}), k = fractional part
      </p>
    </div>
  )
}

