import Task from "./Task"
import Cell from "./Cell"
import StatCell from "./StatCell"

export default function StatRow({
  titleColumnStyle,
  measuresWidth,
  scrollLeft,
  showRewardColumn,
  showTimeColumns = true,
  days,
  totalReward = 0,
  daySums = [],
}) {
  return (
    <div className="stat-row">
      <div className="d-flex">
        <Task
          title="Sum"
          titleColumnStyle={titleColumnStyle}
          measuresWidth={measuresWidth}
          scrollLeft={scrollLeft}
          reward={totalReward}
          showRewardColumn={showRewardColumn}
          showTimeColumns={showTimeColumns}
          timeFrom={undefined}
          timeTo={undefined}
          isMarkedToday={undefined}
        >
          {
            days.map((date, index) => {
              const className = "measure-done"
              const sum = daySums[index] || 0
              
              //  :   ,     floor    {}<small>.{ }</small>
              let formattedSum = sum
              if (typeof sum === 'number' && !isNaN(sum) && sum % 1 !== 0) {
                const rounded = Math.floor(sum * 10) / 10
                const integerPart = Math.floor(rounded)
                const decimalPart = Math.floor((rounded * 10) % 10)
                formattedSum = (
                  <span className="sum-formatted">
                    <span className="sum-integer">{integerPart}</span>
                    <small className="sum-decimal">.{decimalPart}</small>
                  </span>
                )
              }

              return (
                <Cell
                  key={date.id}
                  value={formattedSum}
                  className={className}
                >
                </Cell>
              )
            })
          }
        </Task>
        <StatCell className="task-swipe-handler" value="" />
      </div>
    </div>
  )
}

