import { MEASURE_TYPE_EMPTY, MEASURE_TYPE_NA, MEASURE_TYPE_NUMBER, MEASURE_TYPE_CHECKLIST } from "../models/Measure"

/**
 *   measure  
 *  checklist    "m/n" ( m -   , n -  )
 *  "nan" /      cellMeasure
 *  simple      undefined
 */
export function getMeasureDisplayValue(measure, task, isLastDay = false) {
  if (!task) {
    return undefined
  }

  const taskType = task.type || "simple"

  if (taskType === "checklist") {
    //  checklist   cellMeasure
    if (measure && measure.type === MEASURE_TYPE_CHECKLIST) {
      const cellMeasure = measure.cellMeasure
      
      if (cellMeasure === "nan") {
        return undefined // NaN
      } else if (cellMeasure === "empty") {
        //    -  ,   - "nan"
        return isLastDay ? "" : undefined // NaN
      } else if (cellMeasure === "sum") {
        //  m/n
        const checklist = Array.isArray(measure.value) ? measure.value : []
        const total = checklist.length
        if (total === 0) {
          return "0/0"
        }
        const checked = checklist.filter(item => item.checked).length
        return `${checked}/${total}`
      }
    }
    //  measure     -   
    // (  task.checklist  fallback, ..       )
    return undefined
  } else {
    // simple 
    if (!measure) {
      return undefined
    }
    if (measure.type === MEASURE_TYPE_NUMBER) {
      return measure.value
    }
    return undefined
  }
}

/**
 *    measure
 *  checklist      ()
 *  simple     measure  0
 * @param {Object} measure - Measure 
 * @param {Object} task -  
 * @returns {number}  
 */
export function getMeasureValue(measure, task) {
  if (!task) {
    return 0
  }

  const taskType = task.type || "simple"

  if (taskType === "checklist") {
    //  checklist     
    if (measure && measure.type === MEASURE_TYPE_CHECKLIST && Array.isArray(measure.value)) {
      return measure.value.filter(item => item.checked).length
    }
    return 0
  } else {
    // simple 
    if (!measure) {
      return 0
    }
    if (measure.type === MEASURE_TYPE_NUMBER) {
      return measure.value || 0
    }
    return 0
  }
}

/**
 * ,   measure    
 *  checklist :  true  measure   cellMeasure === "empty"
 *  simple :  true  measure   measure.type === MEASURE_TYPE_EMPTY
 * @param {Object} measure - Measure 
 * @param {Object} task -  
 * @returns {boolean} true  measure 
 */
export function isMeasureEmpty(measure, task) {
  if (!task) {
    return true
  }

  const taskType = task.type || "simple"

  if (taskType === "checklist") {
    //  checklist   cellMeasure
    if (!measure || measure.type !== MEASURE_TYPE_CHECKLIST) {
      return true
    }
    const cellMeasure = measure.cellMeasure || "empty"
    return cellMeasure === "empty"
  } else {
    // simple 
    if (!measure) {
      return true
    }
    return measure.type === MEASURE_TYPE_EMPTY
  }
}

/**
 * ,   measure   NA   
 *  checklist :  true  measure , cellMeasure === "empty"  cellMeasure === "nan"
 *  simple :  true  measure , measure.type === MEASURE_TYPE_EMPTY  measure.type === MEASURE_TYPE_NA
 * @param {Object} measure - Measure 
 * @param {Object} task -  
 * @returns {boolean} true  measure   NA
 */
export function isMeasureEmptyOrNA(measure, task) {
  if (!task) {
    return true
  }

  const taskType = task.type || "simple"

  if (taskType === "checklist") {
    //  checklist   cellMeasure
    if (!measure || measure.type !== MEASURE_TYPE_CHECKLIST) {
      return true
    }
    const cellMeasure = measure.cellMeasure || "empty"
    return cellMeasure === "empty" || cellMeasure === "nan"
  } else {
    // simple 
    if (!measure) {
      return true
    }
    return measure.type === MEASURE_TYPE_EMPTY || measure.type === MEASURE_TYPE_NA
  }
}

/**
 *    measure   
 *  checklist    (0-1)      
 *  simple      0
 */
export function getMeasureNumericValue(measure, task) {
  if (!task) {
    return 0
  }

  const taskType = task.type || "simple"

  if (taskType === "checklist") {
    //  checklist   cellMeasure
    if (measure && measure.type === MEASURE_TYPE_CHECKLIST) {
      const cellMeasure = measure.cellMeasure || "sum"
      
      if (cellMeasure === "nan" || cellMeasure === "empty") {
        return 0
      } else if (cellMeasure === "sum") {
        //     
        const checklist = Array.isArray(measure.value) ? measure.value : []
        const total = checklist.length
        if (total === 0) {
          return 0
        }
        const checked = checklist.filter(item => item.checked).length
        return checked / total
      }
    }
    
    // Fallback:  measure 
    return 0
  } else {
    // simple 
    if (!measure) {
      return 0
    }
    if (measure.type === MEASURE_TYPE_NUMBER) {
      return measure.value || 0
    }
    return 0
  }
}

/**
 *      
 * @param {string} taskId - ID 
 * @param {number} PERIOD -   
 * @param {Array} tasks -   
 * @param {Array} days -   
 * @param {Function} getMeasureValueByTaskIdAndDayId -    measure
 */
export function getFreq(taskId, PERIOD, tasks, days, getMeasureValueByTaskIdAndDayId) {
  let s = 0
  if (days.length === 0) {
    return 0
  }

  const task = tasks.find(t => t.id === taskId)
  if (!task) {
    return 0
  }

  const taskType = task.type || "simple"
  const todayId = days[days.length - 1].id
  const todayMeasure = getMeasureValueByTaskIdAndDayId(taskId, todayId)
  const haveTodayMeasure = taskType === "checklist" 
    ? (todayMeasure.type === MEASURE_TYPE_CHECKLIST)
    : (todayMeasure.type === MEASURE_TYPE_NUMBER)

  let lastDays
  if (haveTodayMeasure) {
    lastDays = days.slice(-PERIOD)
  } else {
    lastDays = days.slice(-PERIOD - 1, -1)
  }

  let count = 0
  for (const { id } of lastDays) {
    const m = getMeasureValueByTaskIdAndDayId(taskId, id)
    const numericValue = getMeasureNumericValue(m, task)
    
    //  checklist     cellMeasure === "sum"
    //  simple     measure.type === NUMBER  EMPTY
    if (taskType === "checklist") {
      if (m.type === MEASURE_TYPE_CHECKLIST) {
        const cellMeasure = m.cellMeasure || "sum"
        if (cellMeasure === "sum") {
          s += Math.max(0, Math.min(1, numericValue))
          count++
        } else if (cellMeasure === "empty" || cellMeasure === "nan") {
          count++
        }
      } else if (m.type === MEASURE_TYPE_EMPTY) {
        count++
      }
    } else {
      // simple 
      if (m.type === MEASURE_TYPE_NUMBER) {
        s += Math.max(0, Math.min(1, numericValue))
        count++
      } else if (m.type === MEASURE_TYPE_EMPTY) {
        count++
      }
    }
  }

  return s / count || 0
}

/**
 *     measure  
 * @param {string} taskId - ID 
 * @param {Array} tasks -   
 * @param {Array} days -   
 * @param {Function} getMeasureValueByTaskIdAndDayId -    measure
 */
export function total(taskId, tasks, days, getMeasureValueByTaskIdAndDayId) {
  const task = tasks.find(t => t.id === taskId)
  if (!task) {
    return 0
  }

  let s = 0
  for (const { id } of days) {
    const m = getMeasureValueByTaskIdAndDayId(taskId, id)
    const numericValue = getMeasureNumericValue(m, task)
    s += numericValue
  }
  return s
}

/**
 * ,     (value >= 1)
 *  checklist :   
 *  simple : measure.value >= 1
 * @param {Object} measure - Measure 
 * @param {Object} task -  
 * @returns {boolean} true    
 */
export function isFullyDone(measure, task) {
  if (isMeasureEmptyOrNA(measure, task)) {
    return false
  }
  return getMeasureNumericValue(measure, task) >= 1
}

/**
 * ,    
 *  simple :  true  measure   ( ,  0  NA,  )
 *  checklist :  true  !isMeasureEmpty  (cellMeasure === "nan"      null  true)
 * @param {Object} measure - Measure 
 * @param {Object} task -  
 * @returns {boolean} true    
 */
export function isFullyMeasured(measure, task) {
  if (!task) {
    return false
  }

  const taskType = task.type || "simple"

  if (taskType === "checklist") {
    //  checklist  ,  measure  
    if (isMeasureEmpty(measure, task)) {
      return false
    }
    
    // ,  measure     
    if (!measure || measure.type !== MEASURE_TYPE_CHECKLIST) {
      return false
    }
    
    const cellMeasure = measure.cellMeasure || "sum"
    
    //  cellMeasure === "nan" (NA),    
    if (cellMeasure === "nan") {
      return true
    }
    
    //  cellMeasure === "sum",  
    if (cellMeasure === "sum") {
      // ,      null  true ( false)
      const checklist = Array.isArray(measure.value) ? measure.value : []
      if (checklist.length === 0) {
        return false
      }
      
      //     null  true (   false)
      return checklist.every(item => item.checked === null || item.checked === true)
    }
    
    //  "empty"  false (  isMeasureEmpty )
    return false
  } else {
    // simple 
    // ,  measure   ( ,  0  NA,  )
    if (isMeasureEmpty(measure, task)) {
      return false
    }
    //  measure.type === MEASURE_TYPE_NUMBER  MEASURE_TYPE_NA,   
    return measure && (measure.type === MEASURE_TYPE_NUMBER || measure.type === MEASURE_TYPE_NA)
  }
}

/**
 * ,     
 *  isFullyMeasured  
 * @param {Object} measure - Measure 
 * @param {Object} task -  
 * @returns {boolean} true     
 */
export function isConsideredMeasuredToday(measure, task) {
  return isFullyMeasured(measure, task)
}

