import { useDispatch, useSelector } from "react-redux"
import { addDay, clearAllByDayId, clearMeasure, collapseTaskGroup, deleteTaskGroup, fetchSystemTime, incMeasure, incTimer, maybeMoveToTop, moveTask, moveTaskGroup, newTask, pauseTimer, removeDayAndMeasuresByDayId, removeTask, resetSystemStartTime, setMeasure, setMeasuresForAllTasksByDayId, setSystemPoints, setSystemStatId, setTimer, startTimer, updateAllTasks, updateTask, updateTaskGroup, pushReward, popReward, getLastReward } from "../store"
import Table from "../components/Table"
import { useEffect, useRef, useState } from "react"
import { Measure } from "../models/Measure"
import { stats } from "../models/Stats"
import { useMeasures } from "../store/measures"
import { useStats } from "../store/stats"
import { floatReward } from "./Reward"

const DAY = 60 * 60 * 24 * 1000
const toList = map => Object.values(map).sort((a, b) => a.index - b.index)

export default function ({
  tasks,
  showTaskHide,
  showTaskUnhide,
}) {
  console.debug("redraw auto-table")

  const dispatch = useDispatch()

  const startTime = useSelector(state => state.system.startTime)
  const settings = useSelector(state => state.system.settings)
  const statId = useSelector(state => state.system.statId)

  const dayMap = useSelector(state => state.dayMap)
  const rewardHistoryState = useSelector(state => state.rewardHistory)
  const dayIndexToTime = index => index * DAY + startTime
  const days = toList(dayMap).map(o => ({ ...o, time: dayIndexToTime(o.index) }))

  const maxDayTime = days.length > 0 && days[days.length - 1].time || 0
  const maxDayIndex = days.length > 0 ? days[days.length - 1].index : null

  const { getMeasureValueByTaskIdAndDayId } = useMeasures()
  const statFunctions = useStats()

  const getStatValueByTaskId = statFunctions[statId]

  const getNextDayIndex = () => (days.length > 0 ? days[days.length - 1].index + 1 : 0)
  const getPrevDayIndex = () => (days.length > 0 ? days[0].index - 1 : 0)

  // TODO
  // const [dateChangeModal, setDateChangeModal] = useState({ show: false, dayId: null })

  let maxDaysToShow = 3
  if (typeof settings.maxDaysToShow === "number") {
    if (0 < settings.maxDaysToShow && settings.maxDaysToShow <= 100500) {
      maxDaysToShow = settings.maxDaysToShow
    }
  }

  function afterMeasure({ taskId, dayId }) {
    dispatch(maybeMoveToTop({ taskId, dayId }))
  }

  //    measure  
  function incMeasureAndPushReward({ measure, taskId, dayId, rewardToAdd, coordinates }) {
    const value = +1
    const reward = (rewardToAdd === null || rewardToAdd === undefined) ? 0 : rewardToAdd
    
    if (dayMap[dayId].index === maxDayIndex && measure.value == -value /* string compare */) {
      dispatch(clearMeasure(Measure.getMeasureIdByTaskIdAndDayId({ taskId, dayId })))
    } else {
      dispatch(incMeasure({ taskId, dayId, value }))
    }
    
    let actualReward = null
    // ,       
    const canRewardThisDay = !settings.rewardTodayOnly || dayId === maxDayIndex
    
    if (settings.autoReward === "uniform" && canRewardThisDay && reward !== 0) {
      const rewardInMs = reward * 1000 * 60
      dispatch(pushReward({ taskId, dayId, reward: rewardInMs }))
      dispatch(incTimer(rewardInMs))
      actualReward = reward
    } else if (settings.autoReward !== "uniform" && canRewardThisDay) {
      //  reward   ( )
      const task = tasks.find(t => t.id === taskId)
      if (task && task.reward) {
        const rewardInMs = (task.reward || 0) * value * 1000 * 60
        dispatch(incTimer(rewardInMs))
        actualReward = task.reward
      }
    }
    
    //   ,      
    if (actualReward !== null && actualReward !== undefined && actualReward !== 0 && coordinates) {
      const container = document.getElementById('table-page') || document.body
      const rewardText = actualReward > 0 ? `+${actualReward}` : `${actualReward}`
      floatReward({
        left: Math.max(0, Math.min(coordinates.left - 100, container.clientWidth - 100)),
        top: Math.max(0, Math.min(coordinates.top, container.clientHeight - 100)),
        value: rewardText,
        container,
        timeout: 2000,
        className: actualReward > 0 ? 'positive' : 'negative',
      })
    }
    
    afterMeasure({ taskId, dayId })
  }

  //    measure  
  function decMeasureAndPopReward({ measure, taskId, dayId, coordinates }) {
    const value = -1
    
    if (dayMap[dayId].index === maxDayIndex && measure.value == -value /* string compare */) {
      dispatch(clearMeasure(Measure.getMeasureIdByTaskIdAndDayId({ taskId, dayId })))
    } else {
      dispatch(incMeasure({ taskId, dayId, value }))
    }
    
    // ,       
    const canRewardThisDay = !settings.rewardTodayOnly || dayId === maxDayIndex

    let actualReward = null
    if (canRewardThisDay) {
    
    if (settings.autoReward === "uniform") {
      const key = `${taskId}.${dayId}`
      const history = rewardHistoryState?.[key]
      if (history && history.length > 0) {
        const lastReward = history[history.length - 1]
        dispatch(popReward({ taskId, dayId }))
        dispatch(incTimer(-lastReward))
        //        
        actualReward = lastReward / (1000 * 60)
      }
    } else {
      //  reward   ( )
      const task = tasks.find(t => t.id === taskId)
      if (task && task.reward) {
        const rewardInMs = (task.reward || 0) * value * 1000 * 60
        dispatch(incTimer(rewardInMs))
        actualReward = task.reward
      }
    }

  }
    
    //   ,      
    if (actualReward !== null && actualReward !== undefined && actualReward !== 0 && coordinates) {
      const container = document.getElementById('table-page') || document.body
      const rewardText = `-${Math.abs(actualReward)}`
      floatReward({
        left: Math.max(0, Math.min(coordinates.left - 100, container.clientWidth - 100)),
        top: Math.max(0, Math.min(coordinates.top, container.clientHeight - 100)),
        value: rewardText,
        container,
        timeout: 2000,
        className: 'negative',
      })
    }
    
    afterMeasure({ taskId, dayId })
  }

  //       / reward
  function onChecklistChange({ taskId, dayId, checkedDelta, reward, coordinates }) {
    if (!reward || checkedDelta === 0) {
      return
    }

    const task = tasks.find(t => t.id === taskId)
    if (!task) {
      return
    }

    // ,       
    const canRewardThisDay = !settings.rewardTodayOnly || dayId === maxDayIndex

    if (!canRewardThisDay) {
      return
    }

    const totalReward = reward * checkedDelta
    const rewardInMs = totalReward * 1000 * 60

    if (settings.autoReward === "uniform") {
      //  uniform :  checkedDelta > 0,  reward  
      //  checkedDelta < 0,   
      if (checkedDelta > 0) {
        //  reward   
        for (let i = 0; i < checkedDelta; i++) {
          dispatch(pushReward({ taskId, dayId, reward: reward * 1000 * 60 }))
        }
        dispatch(incTimer(rewardInMs))
      } else {
        //  reward  
        const key = `${taskId}.${dayId}`
        const history = rewardHistoryState?.[key]
        const rewardsToRemove = Math.min(Math.abs(checkedDelta), history ? history.length : 0)
        for (let i = 0; i < rewardsToRemove; i++) {
          if (history && history.length > 0) {
            const lastReward = history[history.length - 1]
            dispatch(popReward({ taskId, dayId }))
            dispatch(incTimer(-lastReward))
          }
        }
      }
    } else {
      //  -uniform :  / reward
      dispatch(incTimer(rewardInMs))
    }

    //  floating reward (       fallback)
    const container = document.getElementById('table-page') || document.body
    const rewardText = totalReward > 0 ? `+${totalReward}` : `${totalReward}`
    const finalCoordinates = coordinates || {
      left: container.clientWidth / 2,
      top: container.clientHeight / 2
    }
    floatReward({
      left: Math.max(0, Math.min(finalCoordinates.left - 100, container.clientWidth - 100)),
      top: Math.max(0, Math.min(finalCoordinates.top, container.clientHeight - 100)),
      value: rewardText,
      container,
      timeout: 2000,
      className: totalReward > 0 ? 'positive' : 'negative',
    })
  }

  return (
    <Table
      settings={settings}
      tasks={tasks}
      days={days.slice(-maxDaysToShow)}
      stats={stats}
      currentStatId={statId}
      setGroupCollapsed={(groupId, isCollapsed) => dispatch(collapseTaskGroup({ id: groupId, isCollapsed }))}
      onStatChange={id => dispatch(setSystemStatId(id))}
      maxDayTime={days[days.length - 1].time}
      minDayTime={days[0].time}
      getMeasureValueByTaskIdAndDayId={getMeasureValueByTaskIdAndDayId}
      getStatValueByTaskId={getStatValueByTaskId}
      onNewTask={(title, group) => dispatch(newTask({ title, created: maxDayTime, group }))}
      onNewTaskGroup={title => dispatch(newTask({ title, isGroup: true, created: maxDayTime }))}
      onTaskUpdate={task => dispatch(updateTask(task))}
      onTaskDelete={task => dispatch(removeTask(task))}
      onTaskHide={task => dispatch(updateTask({ id: task.id, isHidden: true }))}
      onTaskUnhide={task => dispatch(updateTask({ id: task.id, isHidden: false }))}
      showTaskHide={showTaskHide}
      showTaskUnhide={showTaskUnhide}

      onTaskGroupDelete={group => dispatch(deleteTaskGroup({ id: group.id }))}
      onTaskGroupHide={group => dispatch(updateTaskGroup({ id: group.id, isHidden: true }))}
      onTaskGroupUnhide={group => dispatch(updateTaskGroup({ id: group.id, isHidden: false }))}

      onTaskMove={(unresolvedIndexFrom, unresolvedIndexTo) => dispatch(moveTask({ indexFrom: tasks[unresolvedIndexFrom].index, indexTo: tasks[unresolvedIndexTo].index }))}
      onTaskGroupMove={unresolved => {
        const sliceFrom = tasks[unresolved.sliceFrom].index

        let indexToAfterSplice = unresolved.indexTo
        if (unresolved.indexTo < tasks.length) {
          indexToAfterSplice = tasks[unresolved.indexTo].index
        }
        indexToAfterSplice += + unresolved.spliceDelta

        dispatch(moveTaskGroup({
          count: unresolved.count,
          sliceFrom,
          indexToAfterSplice,
        }))
      }}
      onAddNextDate={() => dispatch(addDay({ index: getNextDayIndex() }))}
      onAddPreviousDate={() => dispatch(addDay({ index: getPrevDayIndex() }))}

      onSetMeasure={({ taskId, dayId, value, cellMeasure }) => {
        dispatch(setMeasure({ taskId, dayId, value, cellMeasure }))
        afterMeasure({ taskId, dayId })
      }}
      onChecklistChange={onChecklistChange}
      incMeasureAndPushReward={incMeasureAndPushReward}
      decMeasureAndPopReward={decMeasureAndPopReward}
      onIncMeasure={({ measure, taskId, dayId, value, rewardToAdd }) => {
        // value -  measure ( +1  -1  autoReward)
        // rewardToAdd -    (  +1)
        
        if (dayMap[dayId].index === maxDayIndex && measure.value == -value /* string compare */) {
          dispatch(clearMeasure(Measure.getMeasureIdByTaskIdAndDayId({ taskId, dayId })))
        } else {
          dispatch(incMeasure({ taskId, dayId, value }))
        }
        
        const task = tasks.find(t => t.id === taskId)
        // ,       
        const canRewardThisDay = !settings.rewardTodayOnly || dayId === maxDayIndex
        
        if (settings.autoReward === "uniform" && canRewardThisDay) {
          if (value > 0 && rewardToAdd !== undefined) {
            // :        
            const rewardInMs = rewardToAdd * 1000 * 60
            dispatch(pushReward({ taskId, dayId, reward: rewardInMs }))
            dispatch(incTimer(rewardInMs))
          } else if (value < 0) {
            // :         
            const key = `${taskId}.${dayId}`
            const history = rewardHistoryState?.[key]
            if (history && history.length > 0) {
              const lastReward = history[history.length - 1]
              dispatch(popReward({ taskId, dayId }))
              dispatch(incTimer(-lastReward))
            }
          }
        } else if (settings.autoReward !== "uniform" && canRewardThisDay) {
          //  reward   ( )
          if (task && task.reward) {
            const reward = (task.reward || 0) * value * 1000 * 60
            dispatch(incTimer(reward))
          }
        }
        afterMeasure({ taskId, dayId, value })
      }}
      onClearMeasure={({ taskId, dayId }) => dispatch(clearMeasure(Measure.getMeasureIdByTaskIdAndDayId({ taskId, dayId })))}

      onRemoveDate={dayId => { window.confirm("r u sure?") && dispatch(removeDayAndMeasuresByDayId(dayId)) }}
      onSetAllNA={dayId => { window.confirm("r u sure?") && dispatch(setMeasuresForAllTasksByDayId({ dayId, value: m => m === undefined ? null : m })) }}
      onSetAllZeros={dayId => { window.confirm("r u sure?") && dispatch(setMeasuresForAllTasksByDayId({ dayId, value: m => m === undefined ? 0 : m })) }}
      onSetAllEmpty={dayId => { window.confirm("r u sure?") && dispatch(clearAllByDayId(dayId)) }}

      onChangeDate={dayId => /*setDateChangeModal({ show: true, dayId })*/ alert("NOT IMPLEMENTED")}
    />
  )
}