import DateCell from "../components/DateCell"
import ScrollableSwipeable from "./ScrollableSwipeable"
import ArrowRight from "./ArrowRight"
import ArrowLeft from "./ArrowLeft"
import Fader from "./Fader"
import * as style from "./style"
import { ContextMenu, ContextMenuItem, ContextMenuTarget } from "./ContextMenu"
import { useState } from "react"
import { useDispatch } from "react-redux"
import { addDaysUntilToday } from "../store"
import useNow from "../hooks/useNow"


export default function ({
  tableWidth,
  days,
  maxDayTime,
  minDayTime,
  onAddPreviousDate,
  onAddNextDate,
  onRemoveDate,
  onChangeDate,
  onSetAllNA,
  onSetAllZeros,
  onSetAllEmpty,
}) {
  // console.debug("days row redraw")

  const dispatch = useDispatch()
  const { now } = useNow()
  const [scrollLeft, setScrollLeft] = useState([0, 0])

  const statsWidth = style.cellWidth
  const measuresWidth = Math.min(days.length * style.cellWidth, Math.max(0, Math.floor(((tableWidth - style.minTitleWidth - statsWidth) / style.cellWidth)) * style.cellWidth))

  // Helper function to get date only (without time)
  const getDateOnly = (timestamp) => {
    const d = new Date(timestamp)
    return new Date(d.getFullYear(), d.getMonth(), d.getDate()).getTime()
  }

  // Check if maxDayTime is already today (or in the future)
  const isMaxDayToday = maxDayTime && getDateOnly(now) <= getDateOnly(maxDayTime)
  
  // Format date as DD/MM
  const formatDate = (timestamp) => {
    const d = new Date(timestamp)
    const day = d.getDate().toString().padStart(2, "0")
    const month = (d.getMonth() + 1).toString().padStart(2, "0")
    return `${day}/${month}`
  }

  // Determine caption and action for right button
  let rightCaption = "Add next day"
  let rightAction = onAddNextDate
  
  if (!isMaxDayToday && maxDayTime) {
    rightCaption = `Forward to ${formatDate(now)}`
    rightAction = () => dispatch(addDaysUntilToday())
  }

  return (
    <ScrollableSwipeable
      initRight={true}
      rightContent={
        <Fader>
          <div style={{ height: "50px", width: style.addDateButtonWidth + "px" }}>
            <ArrowRight caption={rightCaption} />
          </div>
        </Fader>
      }
      leftContent={
        <Fader>
          <div style={{ height: "50px", width: style.addDateButtonWidth + "px" }}>
            <ArrowLeft caption="Add previous day" />
          </div>
        </Fader>
      }
      className="days-wrapper"
      width={measuresWidth}
      onScrollLeft={(wrapped, unwrapped) => setScrollLeft([wrapped, unwrapped])}
      scrollLeft={scrollLeft[0]}
      onLeftAction={onAddPreviousDate}
      onRightAction={rightAction}
    >
      <div className="days">
        {days.map(({ id, time }) => (
          <ContextMenu key={id}>
            <ContextMenuTarget>
              <DateCell value={time} className={time === maxDayTime && "today"} />
            </ContextMenuTarget>
            <ContextMenuItem onClick={() => onSetAllNA(id)}>
              Empty &rarr; N/A
            </ContextMenuItem>
            <ContextMenuItem onClick={() => onSetAllZeros(id)}>
              Empty &rarr; 0
            </ContextMenuItem>
            <ContextMenuItem onClick={() => onSetAllEmpty(id)}>
              Clear all
            </ContextMenuItem>
            <hr />
            <ContextMenuItem onClick={() => onChangeDate(id)}>
              Edit date
            </ContextMenuItem>
            {
              (time === maxDayTime || time === minDayTime)
              &&
              <>
                <ContextMenuItem onClick={() => onRemoveDate(id)}>
                  Delete date
                </ContextMenuItem>
              </>
            }
          </ContextMenu>
        ))}
      </div>
    </ScrollableSwipeable>
  )
}