// Function to save the Redux state to localStorage
const saveStateToLocalStorage = (state) => {
  console.debug("saving to local storage")
  try {
    const serializedState = JSON.stringify(state)
    localStorage.setItem('reduxState', serializedState)
  } catch (err) {
    console.error('Error saving state to localStorage:', err)
  }
}

// Function to load the Redux state from localStorage
const loadStateFromLocalStorage = () => {
  try {
    const serializedState = localStorage.getItem('reduxState')
    if (serializedState === null) {
      return undefined
    }
    return JSON.parse(serializedState)
  } catch (err) {
    console.error('Error loading state from localStorage:', err)
    if (window.confirm("Local storage is broken. Do we start from clean plate?")) {
      return undefined
    } else {
      throw err
    }
  }
}

const dayMap = createSlice({
  name: 'dayMap',
  initialState: {
    0: {
      id: 0,
      index: 0,
    },
    1: {
      id: 1,
      index: 1,
    },
  },
  reducers: {
    addDay(state, { payload: { index } }) {
      const ix = Object.values(state).map(o => o.index)
      if (ix.length > 0) {
        const left = Math.min(...ix)
        const right = Math.max(...ix)
        if (index !== left - 1 && index !== right + 1) {
          throw Error(`Can only add a new day at the head or at the tail`)
        }
      }
      state[index] = { id: index, index }
    },
    delDay(state, { payload: { id } }) {
      const index = state[id].index
      const ix = Object.values(state).map(o => o.index)
      if (ix.length !== 0) {
        const left = Math.min(...ix)
        const right = Math.max(...ix)
        if (index !== left && index !== right) {
          throw Error(`Can only remove head or tail day`)
        }
      } else {
        throw Error(`No days to remove`)
      }
      delete state[id]
    },
  },
})

export const { addDay: addDayAction, delDay } = dayMap.actions

