const measureMap = createSlice({
  name: 'measureMap',
  initialState: {
    "id1.0": {
      id: "id1.0",
      value: 123,
    },
    "id1.1": {
      id: "id1.1",
      value: 122,
    },
  },
  reducers: {
    setMeasure(state, { payload }) {
      const measure = Measure.parse(payload)
      state[measure.id] = measure.data()
    },
    incMeasure(state, { payload: { id, taskId, dayId, value } }) {
      if (typeof value !== "number") {
        console.error(`incMeasure: expected a number, but got ${value} (maybe you wanted to use setMeasure()?)`)
        throw Error("Not a number")
      }

      if (id === undefined) {
        id = Measure.getMeasureIdByTaskIdAndDayId({ taskId, dayId })
      }

      const m = state[id]

      let prev = 0
      if (m && m.value) {
        prev = m.value
      }

      state[id] = new Measure({ id, type: MEASURE_TYPE_NUMBER, value: Math.max(0, prev + value) }).data()
    },
    clearMeasure(state, { payload: id }) {
      /* do not raise error */
      delete state[id]
    },
    clearAllByDayId(state, { payload: dayId }) {
      console.debug("removing measures...")
      for (const m of Object.values(state)) {
        /* compare string with number */
        if (new Measure(m).dayId == dayId) {
          console.debug("remove measure", m.id)
          delete state[m.id]
        }
      }
    }
  },
})

export const { setMeasure, incMeasure, clearMeasure, clearAllByDayId } = measureMap.actions

const taskMap = createSlice({
  name: 'taskMap',
  initialState: {
    id1: {
      id: "id1",
      title: "Hello Task 1",
      reward: 100,
      index: 0,
      isHidden: false,
      created: new Date().getTime(),
    },
    id2: {
      id: "id2",
      title: "Hello Task 2",
      reward: 100,
      index: 1,
      isHidden: false,
      created: new Date().getTime(),
    },
    id3: {
      id: "id3",
      title: "Hello Task 3",
      reward: 999,
      index: 2,
      isHidden: false,
      created: new Date().getTime(),
    },
  },
  reducers: {
    collapseTaskGroup(state, { payload: { id, isCollapsed } }) {
      const group = state[id]
      if (group) {
        group.isCollapsed = isCollapsed
      }
    },
    newTask(state, { payload: { title, created, isGroup, group, type, checklist } }) {

      let index
      if (group) {
        index = group.index + 0.5
      } else {
        index = Math.max(...Object.values(state).map(t => t.index)) + 1
      }

      if (!created) {
        throw Error(`task.created expected`)
      }

      const task = {
        id: uuidv4(),
        isGroup,
        title: title || "",
        reward: null, // auto  
        timeFrom: "",
        timeTo: "",
        timeFromNextDay: false,
        timeToNextDay: false,
        index,
        created,
        checklist: checklist || [], // sortable checklist items
        type: type || "simple", // "simple" or "checklist"
      }

      state[task.id] = task

      Object.values(state).sort((a, b) => a.index - b.index).forEach((item, index) => {
        item.index = index
      })
    },
    moveTaskGroup(state, { payload: {
      sliceFrom,
      count,
      indexToAfterSplice
    } }) {

      const items = Object.values(state).sort((a, b) => a.index - b.index)
      const itemsToMove = items.splice(sliceFrom, count)
      // console.debug("ItemsToMove", itemsToMove.map(t=>t.title))

      items.splice(indexToAfterSplice, 0, ...itemsToMove)
      // console.debug(items.map(t => [t.title, t.index]))

      items.forEach((item, index) => item.index = index)
    },
    moveTask(state, { payload: { indexFrom, indexTo } }) {
      console.debug("moveTask", indexFrom, indexTo)
      
      let taskIdToMove
      for (const [taskId, task] of Object.entries(state)) {
        if (task.index === indexFrom) {
          taskIdToMove = taskId
          break
        }
      }

      if (taskIdToMove) {
        const taskIds = Object.keys(state).sort((a, b) => state[a].index - state[b].index)
        const currentIndex = taskIds.indexOf(taskIdToMove)

        if (currentIndex !== -1) {
          taskIds.splice(currentIndex, 1)
          taskIds.splice(indexTo, 0, taskIdToMove)

          taskIds.forEach((taskId, index) => {
            state[taskId].index = index
          })
        }
      }
    },
    removeTask(state, { payload: { id } }) {
      delete state[id]
      Object
        .values(state)
        .sort((a, b) => a.index - b.index)
        .forEach((t, i) => t.index = i)
    },
    updateTask(state, { payload }) {
      const task = state[payload.id]
      for (const key in payload) {
        task[key] = payload[key]
      }
    },
    updateAllTasks(state, { payload }) {
      for (const task of Object.values(state)) {
        for (const key in payload) {
          task[key] = payload[key]
        }
      }
    },
    deleteTaskGroup(state, { payload }) {

      const items = Object
        .values(state)
        .sort((a, b) => a.index - b.index)

      delete state[payload.id]

      let i = 0
      while (i < items.length && items[i].id !== payload.id) {
        i++
      }

      i++

      while (!items[i].isGroup) {
        delete state[items[i].id]
        i++
      }

      Object
        .values(state)
        .sort((a, b) => a.index - b.index)
        .forEach((t, i) => t.index = i)

    },
    updateTaskGroup(state, { payload }) {

      function update(item) {
        for (const key in payload) {
          if (key !== "id") {
            item[key] = payload[key]
          }
        }
      }

      const items = Object
        .values(state)
        .sort((a, b) => a.index - b.index)

      update(state[payload.id])

      let i = 0

      while (i < items.length && items[i].id !== payload.id) {
        i++
      }

      i++

      while (!items[i].isGroup) {
        update(state[items[i].id])
        i++
      }

    },
    reorderTasks(state, { payload: taskIds }) {
      taskIds.forEach((taskId, index) => {
        state[taskId].index = index
      })
    }
  },
})

export const { moveTask, reorderTasks, updateTaskGroup, deleteTaskGroup, collapseTaskGroup, removeTask, updateTask: updateTaskAction, updateAllTasks, moveTaskGroup } = taskMap.actions

// Thunk  updateTask    
export const updateTask = createAsyncThunk(
  'task/updateTask',
  async (payload, { dispatch, getState }) => {
    const state = getState()
    const task = state.taskMap[payload.id]
    
    if (!task) {
      //   ,   (   )
      dispatch(updateTaskAction(payload))
      return
    }
    
    const oldType = task.type || "simple"
    const newType = payload.type !== undefined ? payload.type : oldType
    const oldChecklist = task.checklist || []
    
    //     checklist,   
    let updatePayload = payload
    if (oldType !== newType && newType === "checklist") {
      updatePayload = { ...payload, isUncollapsed: true }
      
      //     checklist       
      const uncheckAllChecklist = oldChecklist.map(item => ({ ...item, checked: false }))
      updatePayload = { ...updatePayload, checklist: uncheckAllChecklist }
    }
    
    //  
    dispatch(updateTaskAction(updatePayload))
    
    //   ,  measure
    if (oldType !== newType) {
      const days = Object.values(state.dayMap).sort((a, b) => a.index - b.index)
      
      if (days.length > 0) {
        const lastDayId = days[days.length - 1].id
        const measureId = Measure.getMeasureIdByTaskIdAndDayId({ taskId: payload.id, dayId: lastDayId })
        const lastDayMeasure = state.measureMap[measureId]
        
        if (newType === "checklist") {
          //   checklist:
          // 1.    measure
          if (lastDayMeasure) {
            dispatch(clearMeasure(measureId))
          }
          
          // 2.        -   measure  
          const checkedItems = oldChecklist.filter(item => item.checked)
          if (checkedItems.length > 0) {
            //        measure
            const clonedChecklist = oldChecklist.map(item => ({ ...item }))
            dispatch(setMeasure({
              id: measureId,
              type: MEASURE_TYPE_CHECKLIST,
              value: clonedChecklist,
              cellMeasure: "sum",
              taskType: "checklist",
            }))
          }
          //     - measure  undefined (    )
        } else if (newType === "simple") {
          //   simple:  measure   (   checklist )
          if (lastDayMeasure && lastDayMeasure.type === MEASURE_TYPE_CHECKLIST) {
            dispatch(clearMeasure(measureId))
          }
        }
      }
    }
  }
)

