const timer = createSlice({
  name: 'timer',
  initialState: {
    time: 0,
    isRunning: false,
    startTime: null,
  },
  reducers: {
    startTimer(state, { payload: startTime }) {
      if (!startTime) {
        throw Error(`start time is not specified`)
      }
      state.isRunning = true
      state.startTime = startTime
    },
    pauseTimer(state, { payload: stopTime }) {
      const elapsed = stopTime - state.startTime
      state.isRunning = false
      state.time -= elapsed
    },
    setTimer(state, { payload: { time, startTime } }) {
      if (time === undefined) {
        throw Error(`time is not specified`)
      }
      if (startTime === undefined) {
        throw Error(`startTime is not specified`)
      }
      state.time = time
      state.startTime = startTime
    },
    incTimer(state, { payload }) {
      // Support both old format (number) and new format (object)
      let time, currentTime
      if (typeof payload === 'number') {
        time = payload
        currentTime = null
      } else {
        time = payload.time
        currentTime = payload.currentTime
      }
      
      if (state.isRunning && currentTime != null) {
        // If timer is running, we need to account for elapsed time
        const elapsed = currentTime - state.startTime
        state.time = state.time - elapsed + time
        state.startTime = currentTime
      } else {
        state.time += time
      }
    },
  },
})

export const { startTimer, pauseTimer, incTimer, setTimer } = timer.actions

//      (taskId.dayId)
const rewardHistory = createSlice({
  name: 'rewardHistory',
  initialState: {},
  reducers: {
    pushReward(state, { payload: { taskId, dayId, reward } }) {
      const key = `${taskId}.${dayId}`
      if (!state[key]) {
        state[key] = []
      }
      state[key].push(reward)
    },
    popReward(state, { payload: { taskId, dayId } }) {
      const key = `${taskId}.${dayId}`
      if (state[key] && state[key].length > 0) {
        return { ...state, [key]: state[key].slice(0, -1) }
      }
      return state
    },
    clearRewardHistory(state, { payload: { taskId, dayId } }) {
      const key = `${taskId}.${dayId}`
      delete state[key]
    },
  },
})

export const { pushReward, popReward, clearRewardHistory } = rewardHistory.actions

//     
export const getLastReward = (state, taskId, dayId) => {
  const key = `${taskId}.${dayId}`
  const history = state.rewardHistory?.[key]
  if (history && history.length > 0) {
    return history[history.length - 1]
  }
  return null
}

