export const setMeasuresForAllTasksByDayId = createAsyncThunk(
  'taskMap/setOrCreateMeasuresForDayForAllTasks',
  async ({ dayId, value }, { dispatch, getState }) => {
    const state = getState()
    const tasks = state.taskMap

    Object.keys(tasks).forEach(taskId => {
      const measureId = `${taskId}.${dayId}`

      if (typeof value === "function") {
        const m = state.measureMap[measureId]

        try {
          dispatch(setMeasure({ id: measureId, value: value(m && m.value) }))
        } catch (e) {
          console.error(e)
        }
      }
      else if (value === undefined || value === "") {
        dispatch(clearMeasure(measureId))
      } else {
        dispatch(setMeasure({ id: measureId, value }))
      }
    })
  }
)

