export const maybeMoveToTop = createAsyncThunk(
  'tasks/maybeMoveToTop',
  async ({ taskId, dayId }, { dispatch, getState }) => {
    const state = getState()
    if (!state.system.settings.moveDoneToTop) {
      return
    }
    const task = state.taskMap[taskId]
    const measure = state.measureMap[Measure.getMeasureIdByTaskIdAndDayId({ taskId, dayId })]
    if (isFullyDone(measure, task)) {
      dispatch(moveFinishedTasksToTop())
    }
  }
)

export const moveFinishedTasksToTop = createAsyncThunk(
  'tasks/moveToTop',
  async (_, { dispatch, getState }) => {
    console.debug("tasks/moveToTop")

    const { measureMap, dayMap, taskMap } = getState()
    const days = Object.values(dayMap).sort((a, b) => a.index - b.index)
    if (days.length === 0) {
      return
    }

    const lastDayId = days[days.length - 1].id
    const getLastMeasure = taskId => measureMap[Measure.getMeasureIdByTaskIdAndDayId({ taskId, dayId: lastDayId })]
    const isTaskFullyDone = taskId => {
      const measure = getLastMeasure(taskId)
      const task = taskMap[taskId]
      return isFullyDone(measure, task)
    }

    const tasks = Object.values(taskMap).sort((a, b) => a.index - b.index)
    const swap = (i, j) => {
      const t = tasks[i]
      tasks[i] = tasks[j]
      tasks[j] = t
    }

    for (let i = 0; i < tasks.length; i++) {
      for (let j = 1; j < tasks.length; j++) {
        const a = tasks[j - 1]
        const b = tasks[j]
        if (a.isGroup || b.isGroup) {
          continue
        }
        // isFullyDone     ( checklist   )
        if (!isTaskFullyDone(a.id) && isTaskFullyDone(b.id)) {
          swap(j - 1, j)
        }
      }
    }

    dispatch(reorderTasks(tasks.map(t => t.id)))
  }
)

