export const migrateStorage = createAsyncThunk(
  'storage/migrate',
  async (_, { dispatch, getState }) => {
    let v = getState().system.storageVersion

    if (!v) {
      // version 0 => 1
      console.warn(`Updating storage version ${v} => 1`)
      const state = getState()

      Object.values(state.taskMap).forEach(task => {
        if (task.created === undefined) {
          dispatch(updateTask({
            id: task.id,
            created: state.system.startTime,
          }))
        }
      })

      dispatch(setStorageVersion(v = 1))
    }
    if (v === 1) {
      // v1 => v2
      console.warn(`Updating storage version ${v} => 2`)
      const state = getState()

      Object.values(state.measureMap).forEach(m => {
        let type
        if (m.value === null) {
          type = MEASURE_TYPE_NA
        } else if (m.value === undefined) {
          type = MEASURE_TYPE_EMPTY
        } else if (typeof m.value === "number") {
          type = MEASURE_TYPE_NUMBER
        } else {
          console.error("Couldn't migrate measure", m)
          return
        }
        dispatch(setMeasure({
          id: m.id,
          value: m.value,
          type,
        }))
      })

      dispatch(setStorageVersion(v = 2))
    }
    if (v === 2) {
      console.warn(`Updating storage version ${v} => 3`)
      dispatch(system.actions.initSettings())
      dispatch(setStorageVersion(v = 3))
    }
    if (v === 3) {
      // v3 => v4: add type field to existing tasks
      console.warn(`Updating storage version ${v} => 4`)
      const state = getState()

      Object.values(state.taskMap).forEach(task => {
        if (task.type === undefined) {
          dispatch(updateTask({
            id: task.id,
            type: "simple",
          }))
        }
      })

      dispatch(setStorageVersion(v = 4))
    }
  }
)

const DAY = 60 * 60 * 24 * 1000

// Helper function to get date only (without time)
const getDateOnly = (timestamp) => {
  const d = new Date(timestamp)
  return new Date(d.getFullYear(), d.getMonth(), d.getDate()).getTime()
}

// Helper function to convert time to day index
const timeToDayIndex = (time, startTime) => Math.floor((time - startTime) / DAY)

// Function to add all days until today
export const addDaysUntilToday = createAsyncThunk(
  'days/addUntilToday',
  async (_, { dispatch, getState }) => {
    const state = getState()
    const startTime = state.system.startTime
    const now = Date.now()
    
    // Get current day map
    const dayMap = state.dayMap
    const dayList = Object.values(dayMap).sort((a, b) => a.index - b.index)
    
    if (dayList.length === 0) {
      return
    }
    
    const maxDayIndex = dayList[dayList.length - 1].index
    const maxDayTime = maxDayIndex * DAY + startTime
    
    // Calculate today's day index based on current time
    const todayIndex = timeToDayIndex(now, startTime)
    
    // Check if maxDayTime is already today or in the future
    if (todayIndex <= maxDayIndex) {
      return // Already up to date
    }
    
    // Add all days from maxDayIndex + 1 to todayIndex
    for (let index = maxDayIndex + 1; index <= todayIndex; index++) {
      await dispatch(addDay({ index })).unwrap()
    }
  }
)