import { useEffect, useState } from "react";
import ModalWindow from "./ModalWindow";
import CustomInput from "./CustomInput";
import CustomButton from "./CustomButton";
import "./EditTaskModal.scss";

export default function ({ show, task, onSave, onClose }) {
    const [title, setTitle] = useState("");
    const [timeFrom, setTimeFrom] = useState("");
    const [timeTo, setTimeTo] = useState("");
    const [timeFromNextDay, setTimeFromNextDay] = useState(false);
    const [timeToNextDay, setTimeToNextDay] = useState(false);
    const [rewardAuto, setRewardAuto] = useState(false);
    const [reward, setReward] = useState("0");
    const [taskType, setTaskType] = useState("simple");

    useEffect(() => {
        if (show && task) {
            setTitle(task.title || "");
            setTimeFrom(task.timeFrom || "");
            setTimeTo(task.timeTo || "");
            setTimeFromNextDay(task.timeFromNextDay || false);
            setTimeToNextDay(task.timeToNextDay || false);
            const isAuto = task.reward === null;
            setRewardAuto(isAuto);
            setReward(isAuto ? "0" : String(task.reward === 0 ? 0 : (task.reward || 0)));
            setTaskType(task.type || "simple");
        }
    }, [show, task]);

    // Normalize time input: convert "2000" to "20:00", or validate "20:00" format
    function normalizeTime(input) {
        if (!input || input === '' || input === null || input === undefined) {
            return '';
        }
        
        // Remove any whitespace
        const trimmed = input.trim();
        
        // If already in HH:MM format, validate it
        const timeRegex = /^([0-1]?[0-9]|2[0-3]):([0-5][0-9])$/;
        if (timeRegex.test(trimmed)) {
            return trimmed;
        }
        
        // Try to parse as 4-digit number (e.g., "2000" -> "20:00")
        const digitsOnly = trimmed.replace(/[^0-9]/g, '');
        if (digitsOnly.length === 4) {
            const hours = parseInt(digitsOnly.substring(0, 2), 10);
            const minutes = parseInt(digitsOnly.substring(2, 4), 10);
            if (hours >= 0 && hours <= 23 && minutes >= 0 && minutes <= 59) {
                return `${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}`;
            }
        }
        
        // Try to parse as 3-digit number (e.g., "800" -> "08:00")
        if (digitsOnly.length === 3) {
            const hours = parseInt(digitsOnly.substring(0, 1), 10);
            const minutes = parseInt(digitsOnly.substring(1, 3), 10);
            if (hours >= 0 && hours <= 9 && minutes >= 0 && minutes <= 59) {
                return `${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}`;
            }
        }
        
        // Invalid format - return original trimmed value
        return trimmed;
    }

    function handleOK() {
        if (task) {
            let rewardValue = null;
            if (rewardAuto) {
                rewardValue = null; // auto
            } else {
                const inputValue = reward === "" || reward === "-" ? "0" : reward;
                const parsed = parseInt(inputValue, 10);
                rewardValue = isNaN(parsed) ? 0 : parsed;
            }
            onSave({
                id: task.id,
                title: title.trim(),
                timeFrom: normalizeTime(timeFrom),
                timeTo: normalizeTime(timeTo),
                timeFromNextDay: timeFromNextDay,
                timeToNextDay: timeToNextDay,
                reward: rewardValue,
                type: taskType,
            });
        }
        onClose();
    }

    function handleCancel() {
        onClose();
    }

    if (!show || !task) {
        return null;
    }

    return (
        <ModalWindow>
            <div className="edit-task-modal">
                <div className="modal-field">
                    <label>Task Title</label>
                    <CustomInput
                        type="text"
                        value={title}
                        onChange={(e) => setTitle(e.target.value)}
                        placeholder="Enter task title"
                    />
                </div>
                <div className="modal-field">
                    <label>Time From</label>
                    <CustomInput
                        type="text"
                        value={timeFrom}
                        onChange={(e) => setTimeFrom(e.target.value)}
                        onBlur={(e) => {
                            // Use the value from the event, which should be the current input value
                            const inputValue = e.target.value || '';
                            const normalized = normalizeTime(inputValue);
                            // Update if normalized value is different (even if it's the same as current state)
                            // This ensures the display updates correctly
                            setTimeFrom(normalized);
                        }}
                        placeholder="HH:MM or HHMM"
                        maxLength={5}
                    />
                    <label className="checkbox-label">
                        <input
                            type="checkbox"
                            checked={timeFromNextDay}
                            onChange={(e) => setTimeFromNextDay(e.target.checked)}
                        />
                        <span>Next day</span>
                    </label>
                </div>
                <div className="modal-field">
                    <label>Time To</label>
                    <CustomInput
                        type="text"
                        value={timeTo}
                        onChange={(e) => setTimeTo(e.target.value)}
                        onBlur={(e) => {
                            // Use the value from the event, which should be the current input value
                            const inputValue = e.target.value || '';
                            const normalized = normalizeTime(inputValue);
                            // Update if normalized value is different (even if it's the same as current state)
                            // This ensures the display updates correctly
                            setTimeTo(normalized);
                        }}
                        placeholder="HH:MM or HHMM"
                        maxLength={5}
                    />
                    <label className="checkbox-label">
                        <input
                            type="checkbox"
                            checked={timeToNextDay}
                            onChange={(e) => setTimeToNextDay(e.target.checked)}
                        />
                        <span>Next day</span>
                    </label>
                </div>
                <div className="modal-field">
                    <label>Task Type</label>
                    <select
                        value={taskType}
                        onChange={(e) => setTaskType(e.target.value)}
                        style={{
                            width: "100%",
                            padding: "8px",
                            fontSize: "14px",
                            border: "1px solid #ccc",
                            borderRadius: "4px",
                        }}
                    >
                        <option value="simple">Simple</option>
                        <option value="checklist">Checklist</option>
                    </select>
                </div>
                <div className="modal-field">
                    <label>Reward</label>
                    <div className="reward-field">
                        <label className="checkbox-label">
                            <input
                                type="checkbox"
                                checked={rewardAuto}
                                onChange={(e) => {
                                    setRewardAuto(e.target.checked);
                                    if (e.target.checked) {
                                        setReward("0");
                                    }
                                }}
                            />
                            <span>Auto</span>
                        </label>
                        <CustomInput
                            type="number"
                            value={reward}
                            onChange={(e) => setReward(e.target.value)}
                            placeholder="0"
                            disabled={rewardAuto}
                        />
                    </div>
                </div>
                <div className="modal-buttons">
                    <CustomButton onClick={handleOK}>OK</CustomButton>
                    <CustomButton onClick={handleCancel}>Cancel</CustomButton>
                </div>
            </div>
        </ModalWindow>
    );
}

