import { useEffect, useRef, useState } from "react"
import Task from "../components/Task"
import { SortableItem, SortableDragHandler } from "../components/Sortable"
import NewTask from "./NewTask"
import CustomButton from "./CustomButton"
import { Swipeable, SwipeableLeftAction, SwipeableRightAction } from "./Swipeable"
import popupError from "./PopupError"
import { hasParentWithClass } from "../utils/html"
import useResize from "../hooks/useResize"
import * as style from "./style"
import StatCell from "./StatCell"
import Overlay, { CaptureOverlay } from "./Overlay"
import Cell, { CellAction } from "./Cell"
import StatRow from "./StatRow"
import { MEASURE_TYPE_EMPTY, MEASURE_TYPE_NUMBER, MEASURE_TYPE_CHECKLIST, MEASURE_TYPE_NA } from "../models/Measure"
import SortableGrouped from "./SortableGrouped"
import GroupHeader from "./GroupHeader"
import DragHandler from "./DragHandler"

import "./Table.scss"
import AutoDaysRow from "./AutoDaysRow"
import Constrained from "./Constrained"
import EditTaskModal from "./EditTaskModal"
import Checklist from "./Checklist"
import { getMeasureDisplayValue, getMeasureNumericValue, isConsideredMeasuredToday, getMeasureValue, isMeasureEmpty, isMeasureEmptyOrNA } from "../utils/taskUtils"


export default function ({
  tasks,
  days,
  stats,
  currentStatId,
  maxDayTime,
  minDayTime,
  getMeasureValueByTaskIdAndDayId,
  getStatValueByTaskId,
  onTaskUpdate,
  onTaskDelete,
  onNewTask,
  onNewTaskGroup,
  onTaskMove,
  onAddPreviousDate,
  onAddNextDate,
  onSetMeasure,
  onIncMeasure,
  onChecklistChange,
  incMeasureAndPushReward,
  decMeasureAndPopReward,
  onClearMeasure,
  setGroupCollapsed,
  onRemoveDate,
  onChangeDate,
  onSetAllNA,
  onSetAllZeros,
  onSetAllEmpty,
  onTaskHide,
  onTaskUnhide,
  showTaskHide,
  showTaskUnhide,
  onStatChange,
  onTaskGroupMove,
  onTaskGroupDelete,
  onTaskGroupHide,
  onTaskGroupUnhide,
  settings,
}) {
  console.debug("redraw table")

  settings = settings || {}
  const autoReward = settings.autoReward === "uniform"
  const autoHideDone = !!settings.hideDone
  const autoRewardMean = parseInt(settings.meanReward) || 0
  const autoRewardDeviation = parseInt(settings.deviationReward) || 0

  const [tableWidth, setTableWidth] = useState(0)
  const [scrollLeft, setScrollLeft] = useState([0, 0])
  const [editingTask, setEditingTask] = useState(null)

  //    reward  
  function calcNewReward(taskId, dayId) {
    const task = tasks.find(t => t.id === taskId)
    if (!task) {
      return undefined
    }

    // ,       
    const maxDayId = days.length > 0 ? days[days.length - 1].id : null
    const isToday = dayId === maxDayId
    const canRewardThisDay = !settings.rewardTodayOnly || isToday

    if (!canRewardThisDay) {
      return undefined
    }

    if (autoReward) {
      // Uniform reward 
      if (task.reward === null) {
        //    mean  deviation  auto
        return parseInt(autoRewardMean - autoRewardDeviation + 2 * autoRewardDeviation * Math.random())
      } else if (typeof task.reward === 'number') {
        //   
        return task.reward
      } else {
        // Fallback    mean  deviation
        return parseInt(autoRewardMean - autoRewardDeviation + 2 * autoRewardDeviation * Math.random())
      }
    } else {
      //  
      if (task.reward !== null && typeof task.reward === 'number') {
        //  reward  ,   
        return task.reward
      } else {
        return undefined
      }
    }
  }

  const statsWidth = style.cellWidth
  const rewardColumnWidth = !autoReward ? style.cellWidth : 0
  const timeColumnsWidth = 2 * style.cellWidth // T1 and T2

  // ,        
  //     
  const desiredMeasuresWidth = days.length * style.cellWidth

  //     
  let showTimeColumns = true
  let showRewardColumn = !autoReward

  // ,       +   
  if (tableWidth > 0) {
    const requiredWidthWithAll = style.minTitleWidth + statsWidth + rewardColumnWidth + timeColumnsWidth + desiredMeasuresWidth
    if (tableWidth < requiredWidthWithAll) {
      //      ,  TIME 
      showTimeColumns = false
      console.debug(`not enough space for time columns, need ${requiredWidthWithAll}px, have ${tableWidth}px`)

      // ,     TIME    
      const requiredWidthWithoutTime = style.minTitleWidth + statsWidth + rewardColumnWidth + desiredMeasuresWidth
      if (tableWidth < requiredWidthWithoutTime) {
        //       ,  REWARD 
        showRewardColumn = false
        console.debug(`not enough space for reward column, need ${requiredWidthWithoutTime}px, have ${tableWidth}px`)
      }
    } else {
      console.debug(`enough space for time columns, need ${requiredWidthWithAll}px, have ${tableWidth}px`)
    }
  }

  const actualRewardColumnWidth = showRewardColumn ? rewardColumnWidth : 0
  const actualTimeColumnsWidth = showTimeColumns ? timeColumnsWidth : 0

  const measuresWidth = Math.min(desiredMeasuresWidth, Math.max(0, Math.floor(((tableWidth - style.minTitleWidth - statsWidth - actualRewardColumnWidth - actualTimeColumnsWidth) / style.cellWidth)) * style.cellWidth))
  const nodeRef = useRef()

  const titleWidth = Math.max(style.minTitleWidth, tableWidth - measuresWidth - statsWidth - actualRewardColumnWidth - actualTimeColumnsWidth)
  const titleColumnStyle = {
    width: titleWidth + "px"
  }

  useEffect(() => {
    if (tableWidth > 0 && measuresWidth === 0) {
      popupError("YOUR SCREEN IS TOO SMALL FOR THE TABLE")
    }
  }, [tableWidth])

  useResize(nodeRef, node => setTableWidth(node.offsetWidth))

  const currentStat = stats.find(item => item.id === currentStatId)
  const currentStatTitle = currentStat && currentStat.title || "???"

  function findGroupIndexFromIndex(i) {
    while (i < tasks.length && !tasks[i].isGroup) {
      i++
    }
    return i
  }

  const [isSorting, setSorting] = useState(true)
  let __lastGroup = null

  //    n/m  
  function getGroupProgress(groupIndex) {
    if (groupIndex < 0 || groupIndex >= tasks.length || !tasks[groupIndex].isGroup) {
      return null
    }

    const lastDay = days.length > 0 ? days[days.length - 1] : null
    if (!lastDay) {
      return null
    }

    //        
    const groupTasks = []
    for (let i = groupIndex + 1; i < tasks.length; i++) {
      if (tasks[i].isGroup) {
        break //  
      }
      groupTasks.push(tasks[i])
    }

    let n = 0 //  
    let m = 0 //    NA

    groupTasks.forEach(task => {
      const measure = getMeasureValueByTaskIdAndDayId(task.id, lastDay.id)
      
      // ,  NA  
      if (measure && measure.type === MEASURE_TYPE_NA) {
        return //    m
      }

      m++ //    m

      //  n:    value >= 1 (fully done)
      if (measure) {
        const value = getMeasureNumericValue(measure, task)
        if (value >= 1) {
          n += 1
        }
      }
    })

    return { n, m }
  }

  //    StatRow
  const nonGroupTasks = tasks.filter(task => !task.isGroup)
  const totalReward = nonGroupTasks.reduce((sum, task) => sum + (task.reward || 0), 0)
  const daySums = days.map(day => {
    return nonGroupTasks.reduce((sum, task) => {
      const measure = getMeasureValueByTaskIdAndDayId(task.id, day.id)
      const numericValue = getMeasureNumericValue(measure, task)
      return sum + numericValue
    }, 0)
  })

  return (
    <div ref={nodeRef} className="task-table">
      <div className="table-head">
        <CustomButton className="task-title-column" style={titleColumnStyle}>
        </CustomButton>
        {showRewardColumn && (
          <CustomButton className="date-cell cell time-header" style={{ minWidth: style.cellWidth + "px", width: style.cellWidth + "px", fontSize: "10px" }}>
            REWARD
          </CustomButton>
        )}
        {showTimeColumns && (
          <>
            <CustomButton className="date-cell cell time-header" style={{ minWidth: style.cellWidth + "px", width: style.cellWidth + "px", fontSize: "10px" }}>
              TIME<br />FROM
            </CustomButton>
            <CustomButton className="date-cell cell time-header" style={{ minWidth: style.cellWidth + "px", width: style.cellWidth + "px", fontSize: "10px" }}>
              TIME<br />TIL
            </CustomButton>
          </>
        )}
        <AutoDaysRow
          tableWidth={tableWidth}
          days={days}
          maxDayTime={maxDayTime}
          minDayTime={minDayTime}
          onAddPreviousDate={onAddPreviousDate}
          onAddNextDate={onAddNextDate}
          onRemoveDate={onRemoveDate}
          onChangeDate={onChangeDate}
          onSetAllNA={onSetAllNA}
          onSetAllZeros={onSetAllZeros}
          onSetAllEmpty={onSetAllEmpty}
        />
        <div className="stat-cell-head-title">
          <Overlay
            base={
              <CustomButton className="current-stat-title">
                {currentStatTitle}
              </CustomButton>
            }
            overlay={
              <div className="change-stat-overlay">
                {
                  stats
                    .filter(stat => stat.id !== currentStatId)
                    .map(({ id, title }) =>
                      <CaptureOverlay key={id}>
                        <CustomButton className="action-button" onClick={() => onStatChange(id)}>
                          {title}
                        </CustomButton>
                      </CaptureOverlay>
                    )
                }
                <CustomButton className="action-button">
                  {currentStatTitle}
                </CustomButton>
              </div>
            }
          />
        </div>
      </div>

      <div className="tasks" style={{
      }}>
        <SortableGrouped
          style={{
            minHeight: isSorting ? undefined : "100vh"
          }}
          findGroupIndexFromIndex={findGroupIndexFromIndex}
          onSimpleItemMove={onTaskMove}
          onGroupMove={onTaskGroupMove}
          isSimpleItemIndex={index => !tasks[index].isGroup}
          onStart={(e, indexFrom) => {
            if (tasks[indexFrom].isGroup) {
              setSorting(false)
              // console.debug(">>>", nodeRef)
              // document.documentElement.scrollTop = nodeRef.current.getBoundingClientRect().top
              nodeRef.current.scrollIntoView()
            }
          }}
          onFinish={() => setSorting(true)}
        >
          {
            tasks.map((item, index) => {
              if (item.isGroup) {
                const group = __lastGroup = item
                const progress = getGroupProgress(index)
                return (
                  <SortableItem key={group.id}>
                    <div>
                      <Swipeable>
                        <SwipeableRightAction width={120} onAction={() => onTaskGroupDelete(group)}>
                          Delete
                        </SwipeableRightAction>
                        {
                          showTaskHide &&
                          <SwipeableRightAction width={120} onAction={() => onTaskGroupHide(group)}>
                            Hide
                          </SwipeableRightAction>
                        }
                        {
                          showTaskUnhide &&
                          <SwipeableRightAction width={120} onAction={() => onTaskGroupUnhide(group)}>
                            Unhide
                          </SwipeableRightAction>
                        }

                        <GroupHeader
                          isCollapsed={group.isCollapsed || !isSorting}
                          onCollapse={() => setGroupCollapsed(group.id, !group.isCollapsed)}
                          title={group.title}
                          onTitleChange={title => onTaskUpdate({ id: group.id, title })}
                          onAddNewTask={() => onNewTask("", group)}
                          progress={progress}
                        />
                      </Swipeable>
                    </div>
                  </SortableItem>
                )
              } else {

                const task = item

                //      
                if (__lastGroup !== null && __lastGroup.isCollapsed) {
                  return (
                    <SortableItem key={task.id}>
                    </SortableItem>
                  )
                }

                let sortClassName = "task-wrapper "

                if (task.isCollapsed) {
                  sortClassName += `task-collapsed `
                }

                if (!isSorting) {
                  sortClassName += `task-not-displayed `
                }

                if (days.length > 0) {
                  const todayMeasure = getMeasureValueByTaskIdAndDayId(task.id, days[days.length - 1].id)
                  if (isConsideredMeasuredToday(todayMeasure, task)) {
                    sortClassName += "today-measured "
                    if (autoHideDone) {
                      sortClassName += "task-not-displayed "
                    }
                  }
                }

                return (
                  <SortableItem key={task.id}>
                    <div className={sortClassName}>
                      <Swipeable filterTouches={el => {
                        return hasParentWithClass(el, "task-swipe-handler") && !hasParentWithClass(el, "measures")
                      }}>

                        <SwipeableLeftAction width={120} onAction={() => setEditingTask(task)}>
                          Edit
                        </SwipeableLeftAction>

                        {
                          autoReward &&
                          task.reward !== null &&
                          <SwipeableLeftAction width={120} onAction={() => onTaskUpdate({ id: task.id, reward: null })}>
                            Set auto reward
                          </SwipeableLeftAction>
                        }

                        <SwipeableRightAction width={120} onAction={() => onTaskDelete(task)}>
                          Delete
                        </SwipeableRightAction>
                        {
                          showTaskHide &&
                          <SwipeableRightAction width={120} onAction={() => onTaskHide(task)}>
                            Hide
                          </SwipeableRightAction>
                        }
                        {
                          showTaskUnhide &&
                          <SwipeableRightAction width={120} onAction={() => onTaskUnhide(task)}>
                            Unhide
                          </SwipeableRightAction>
                        }
                        <div className="d-flex">
                          {(() => {
                            //  in/out time range       
                            const lastDayId = days.length > 0 ? days[days.length - 1].id : null
                            const lastDayMeasure = lastDayId ? getMeasureValueByTaskIdAndDayId(task.id, lastDayId) : null
                            const isLastDayMarked = lastDayMeasure && lastDayMeasure.type !== MEASURE_TYPE_EMPTY

                            let displayFixedRewardText = null
                            let displayFixedRewardClassName = "badge-neutral"
                            if (settings.autoReward === "uniform" && task.reward === null) {
                              const minValue = autoRewardMean - autoRewardDeviation
                              const maxValue = autoRewardMean + autoRewardDeviation

                              // 
                              displayFixedRewardText = `+${minValue}..${maxValue}`
                              displayFixedRewardClassName = "badge-positive"
                            } else {
                              if (typeof task.reward === "number") {
                                if (!task.reward) {
                                  // don't display at all
                                  displayFixedRewardText = null
                                } else {
                                  displayFixedRewardClassName = task.reward > 0 ? "badge-positive" : "badge-negative"
                                }
                              }
                            }


                            return (
                              <Task
                                title={task.title}
                                titleColumnStyle={titleColumnStyle}
                                measuresWidth={measuresWidth}
                                scrollLeft={scrollLeft[1]}
                                onTitleChange={title => onTaskUpdate({ id: task.id, title })}
                                reward={task.reward}
                                onRewardChange={(reward => onTaskUpdate({ id: task.id, reward }))}
                                showRewardColumn={showRewardColumn}
                                showTimeColumns={showTimeColumns}
                                timeFrom={task.timeFrom}
                                onTimeFromChange={timeFrom => onTaskUpdate({ id: task.id, timeFrom })}
                                timeTo={task.timeTo}
                                onTimeToChange={timeTo => onTaskUpdate({ id: task.id, timeTo })}
                                timeFromNextDay={task.timeFromNextDay}
                                timeToNextDay={task.timeToNextDay}
                                isMarkedToday={isLastDayMarked}
                                maxDayTime={maxDayTime}
                                displayFixedRewardText={displayFixedRewardText}
                                displayFixedRewardClassName={displayFixedRewardClassName}
                                isUncollapsed={task.isUncollapsed}
                                onToggleUncollapsed={() => onTaskUpdate({ id: task.id, isUncollapsed: !task.isUncollapsed })}
                              >
                                {
                                  days
                                    .map(date => getMeasureValueByTaskIdAndDayId(task.id, date.id))
                                    .map((measure, index) => {
                                      const taskId = task.id
                                      const dayId = days[index].id
                                      const measureIndex = index
                                      //    :
                                      // 1.  measure  taskType -  
                                      // 2.  ,     measure
                                      // 3. Fallback  task.type   measures
                                      let effectiveTaskType
                                      if (measure.taskType) {
                                        effectiveTaskType = measure.taskType
                                      } else if (measure.type === MEASURE_TYPE_CHECKLIST) {
                                        effectiveTaskType = "checklist"
                                      } else if (measure.type === MEASURE_TYPE_NUMBER) {
                                        effectiveTaskType = "simple"
                                      } else {
                                        //  /NA measures    
                                        effectiveTaskType = task.type || "simple"
                                      }
                                      const taskType = effectiveTaskType
                                      const isChecklist = taskType === "checklist"
                                      const isLastDay = index === days.length - 1

                                      //    checklist   cellMeasure
                                      let measureTypeClass = "measure-" + measure.type
                                      let checklistFullDone = false
                                      let checklistZero = false
                                      let checklistPartDone = false

                                      if (isChecklist && measure.type === MEASURE_TYPE_CHECKLIST) {
                                        const cellMeasure = measure.cellMeasure || "sum"
                                        if (cellMeasure === "nan") {
                                          measureTypeClass = "measure-na"
                                        } else if (cellMeasure === "empty") {
                                          measureTypeClass = isLastDay ? "measure-empty" : "measure-na"
                                        } else {
                                          measureTypeClass = "measure-checklist"

                                          //  full-done  zero  checklist
                                          if (cellMeasure === "sum") {
                                            const numericValue = getMeasureNumericValue(measure, task)
                                            if (numericValue >= 1) {
                                              checklistFullDone = true
                                            } else if (numericValue > 0) {
                                              checklistPartDone = true
                                            } else {
                                              checklistZero = true
                                            }
                                          }
                                        }
                                      }
                                      
                                      const className = [
                                        isLastDay && "today",
                                        measureTypeClass,
                                        measure.type === "number" && measure.value === 0 && "measure-zero",
                                        measure.type === "number" && measure.value < 1 && "measure-part-done",
                                        measure.type === "number" && measure.value >= 1 && "measure-full-done",
                                        measure.type === "number" && measure.value > 0 && "measure-positive",
                                        isChecklist && "measure-checklist",
                                        checklistFullDone && "measure-full-done",
                                        checklistZero && "measure-zero",
                                        checklistPartDone && "measure-part-done",
                                      ].join(" ")

                                      //  checklist   m/n,  simple - 
                                      const displayValue = isChecklist
                                        ? getMeasureDisplayValue(measure, task, isLastDay)
                                        : (measure.type === MEASURE_TYPE_NUMBER ? measure.value : undefined)

                                      const value = displayValue
                                      const sub = measure.type === "number" && measure.value > 0 && Math.min(measure.value, 1)

                                      //  :  measure  ,   task.checklist
                                      const getChecklistItems = () => {
                                        if (measure && measure.type === MEASURE_TYPE_CHECKLIST && Array.isArray(measure.value)) {
                                          return measure.value
                                        }
                                        return (task.checklist || []).map(item => ({ ...item, checked: false }))
                                      }

                                      //    checklist 
                                      const handleChecklistNA = (e) => {
                                        // NA    -  measure    cellMeasure = "nan"
                                        const checklist = getChecklistItems()
                                        
                                        // ,    
                                        const oldCheckedCount = checklist.filter(item => item.checked).length
                                        
                                        //    ,  
                                        if (oldCheckedCount > 0 && task.reward && onChecklistChange) {
                                          const checkedDelta = -oldCheckedCount
                                          
                                          //     
                                          let coordinates = null
                                          if (e && e.currentTarget) {
                                            const rect = e.currentTarget.getBoundingClientRect()
                                            const container = document.getElementById('table-page') || document.body
                                            const containerRect = container.getBoundingClientRect()

                                            coordinates = {
                                              left: rect.left - containerRect.left + rect.width / 2,
                                              top: rect.top - containerRect.top
                                            }
                                          }
                                          
                                          onChecklistChange({
                                            taskId,
                                            dayId,
                                            checkedDelta,
                                            reward: task.reward,
                                            coordinates
                                          })
                                        }
                                        
                                        onSetMeasure({
                                          taskId,
                                          dayId,
                                          value: checklist,
                                          cellMeasure: "nan",
                                          taskType: "checklist"
                                        })
                                      }

                                      const handleChecklistEmpty = () => {
                                        //  measure  -   undefined
                                        onClearMeasure({ taskId, dayId })
                                      }

                                      const handleChecklistZero = (e) => {
                                        // 0 -  measure     cellMeasure = "sum"
                                        const checklist = getChecklistItems()
                                        const oldCheckedCount = checklist.filter(item => item.checked).length
                                        const uncheckedChecklist = checklist.map(item => ({ ...item, checked: false }))
                                        const checkedDelta = -oldCheckedCount

                                        onSetMeasure({
                                          taskId,
                                          dayId,
                                          value: uncheckedChecklist,
                                          cellMeasure: "sum",
                                          taskType: "checklist"
                                        })

                                        //  reward   
                                        if (checkedDelta !== 0 && task.reward && onChecklistChange) {
                                          let coordinates = null
                                          if (e && e.currentTarget) {
                                            const rect = e.currentTarget.getBoundingClientRect()
                                            const container = document.getElementById('table-page') || document.body
                                            const containerRect = container.getBoundingClientRect()

                                            coordinates = {
                                              left: rect.left - containerRect.left + rect.width / 2,
                                              top: rect.top - containerRect.top
                                            }
                                          }

                                          onChecklistChange({
                                            taskId,
                                            dayId,
                                            checkedDelta,
                                            reward: task.reward,
                                            coordinates
                                          })
                                        }
                                      }

                                      const handleChecklistCheck = (e) => {
                                        //  -  measure     cellMeasure = "sum"
                                        const checklist = getChecklistItems()
                                        //  measure   NA,  oldCheckedCount   0
                                        //    NA    
                                        const isNA = measure && measure.type === MEASURE_TYPE_CHECKLIST && measure.cellMeasure === "nan"
                                        const oldCheckedCount = isNA ? 0 : checklist.filter(item => item.checked).length
                                        const checkedChecklist = checklist.map(item => ({ ...item, checked: true }))
                                        const newCheckedCount = checkedChecklist.length
                                        const checkedDelta = newCheckedCount - oldCheckedCount

                                        onSetMeasure({
                                          taskId,
                                          dayId,
                                          value: checkedChecklist,
                                          cellMeasure: "sum",
                                          taskType: "checklist"
                                        })

                                        //  reward   
                                        if (checkedDelta !== 0 && task.reward && onChecklistChange) {
                                          let coordinates = null
                                          if (e && e.currentTarget) {
                                            const rect = e.currentTarget.getBoundingClientRect()
                                            const container = document.getElementById('table-page') || document.body
                                            const containerRect = container.getBoundingClientRect()

                                            coordinates = {
                                              left: rect.left - containerRect.left + rect.width / 2,
                                              top: rect.top - containerRect.top
                                            }
                                          }

                                          onChecklistChange({
                                            taskId,
                                            dayId,
                                            checkedDelta,
                                            reward: task.reward,
                                            coordinates
                                          })
                                        }
                                      }

                                      return (
                                        <Cell
                                          key={index}
                                          value={value}
                                          onChange={isChecklist ? undefined : (value => onSetMeasure({ taskId, dayId, value }))}
                                          className={className}
                                        >
                                          {
                                            isChecklist ? (
                                              <>
                                                <CellAction className={`left-2 action-na ${className}`} onAction={(e) => handleChecklistNA(e)}>NA</CellAction>
                                                {isLastDay && (
                                                  <CellAction className={`left-1 action-empty ${className}`} onAction={handleChecklistEmpty}></CellAction>
                                                )}
                                                {(getMeasureValue(measure, task) > 0 || isMeasureEmptyOrNA(measure, task)) && (
                                                  <CellAction className={`left-1 action-zero ${className}`} onAction={(e) => handleChecklistZero(e)}>0</CellAction>
                                                )}
                                                <CellAction className={`right-1 action-check ${className}`} onAction={(e) => handleChecklistCheck(e)}></CellAction>
                                              </>
                                            ) : (
                                              <>
                                                <CellAction className={`left-2 action-na    ${className}`} onAction={() => onSetMeasure({ taskId, dayId, value: null })}>NA</CellAction>
                                                <CellAction className={`left-1 action-empty ${className}`} onAction={() => onSetMeasure({ taskId, dayId, value: undefined })}></CellAction>
                                                <CellAction className={`left-1 action-zero  ${className}`} onAction={() => onSetMeasure({ taskId, dayId, value: 0 })}>0</CellAction>
                                                <CellAction className={`right-1 action-plus ${className}`} onAction={(e) => {
                                                  const rewardToAdd = calcNewReward(taskId, dayId)

                                                  //     
                                                  const rect = e.currentTarget.getBoundingClientRect()
                                                  const container = document.getElementById('table-page') || document.body
                                                  const containerRect = container.getBoundingClientRect()

                                                  const coordinates = {
                                                    left: rect.left - containerRect.left + rect.width / 2,
                                                    top: rect.top - containerRect.top
                                                  }

                                                  incMeasureAndPushReward({ measure, taskId, dayId, rewardToAdd, coordinates })
                                                }}>+1</CellAction>
                                                <CellAction className={`left-1 action-minus ${className}`} onAction={(e) => {
                                                  //     
                                                  const rect = e.currentTarget.getBoundingClientRect()
                                                  const container = document.getElementById('table-page') || document.body
                                                  const containerRect = container.getBoundingClientRect()

                                                  const coordinates = {
                                                    left: rect.left - containerRect.left + rect.width / 2,
                                                    top: rect.top - containerRect.top
                                                  }

                                                  decMeasureAndPopReward({ measure, taskId, dayId, coordinates })
                                                }}>-{sub}</CellAction>
                                              </>
                                            )
                                          }
                                        </Cell>
                                      )
                                    })
                                }
                              </Task>
                            )
                          })()}
                          <StatCell className="task-swipe-handler" value={getStatValueByTaskId(task.id)} />
                        </div>
                      </Swipeable>
                      {task.isUncollapsed && (() => {
                        //     :
                        // -  checklist :   measure
                        // -  simple :   
                        const isChecklistTask = task.type === "checklist"
                        const lastDayId = days.length > 0 ? days[days.length - 1].id : null
                        const lastDayMeasure = lastDayId ? getMeasureValueByTaskIdAndDayId(task.id, lastDayId) : null
                        const isToday = lastDayId !== null

                        //    
                        //  measure  ( checklist ) -  ,  
                        let checklistItems = (task.checklist || []).map(item => ({ ...item, checked: false }))
                        if (isChecklistTask && isToday && lastDayMeasure && lastDayMeasure.type === MEASURE_TYPE_CHECKLIST && Array.isArray(lastDayMeasure.value)) {
                          //     checklist     measure
                          checklistItems = lastDayMeasure.value
                        } else if (!isChecklistTask) {
                          //  simple   task.checklist   
                          checklistItems = task.checklist || []
                        }

                        return (
                          <Checklist
                            items={checklistItems}
                            onChange={(checklist, checkedDelta, coordinates) => {
                              //  task.checklist
                              onTaskUpdate({ id: task.id, checklist, checklistExpanded: true })

                              //  checklist    measure  
                              if (isChecklistTask && isToday && lastDayId) {
                                //      
                                const oldCheckedCount = checklistItems.filter(item => item.checked).length
                                const newCheckedCount = checklist.filter(item => item.checked).length
                                const actualCheckedDelta = checkedDelta !== undefined ? checkedDelta : (newCheckedCount - oldCheckedCount)

                                onSetMeasure({
                                  taskId: task.id,
                                  dayId: lastDayId,
                                  value: checklist,
                                  cellMeasure: "sum",
                                  taskType: "checklist" //     measure
                                })

                                // / reward     
                                if (actualCheckedDelta !== 0 && task.reward && onChecklistChange) {
                                  onChecklistChange({
                                    taskId: task.id,
                                    dayId: lastDayId,
                                    checkedDelta: actualCheckedDelta,
                                    reward: task.reward,
                                    coordinates
                                  })
                                }
                              }
                            }}
                            expanded={task.checklistExpanded === true || checklistItems.length === 0}
                            onToggleExpand={() => onTaskUpdate({ id: task.id, checklistExpanded: !(task.checklistExpanded === true) })}
                          />
                        )
                      })()}
                    </div>

                  </SortableItem>
                )
              }
            })
          }
        </SortableGrouped>


        <div className="table-footer">
          <div className="task-title-column" style={titleColumnStyle}>
            <div className="margin"></div>
            <div className="title">
              <NewTask onNewTask={onNewTask} />
            </div>
          </div>
          <div className="task-title-column" style={titleColumnStyle}>
            <div className="margin"></div>
            <div className="title">
              <NewTask onNewTask={onNewTaskGroup} placeholder="New Group" />
            </div>
          </div>
        </div>
      </div>


      <StatRow
        titleColumnStyle={titleColumnStyle}
        measuresWidth={measuresWidth}
        scrollLeft={scrollLeft[1]}
        showRewardColumn={showRewardColumn}
        showTimeColumns={showTimeColumns}
        days={days}
        totalReward={totalReward}
        daySums={daySums}
      />


      <EditTaskModal
        show={editingTask !== null}
        task={editingTask}
        onSave={(updatedTask) => {
          onTaskUpdate(updatedTask)
          setEditingTask(null)
        }}
        onClose={() => setEditingTask(null)}
      />


    </div >
  )
}
