import ArrowHandler from "./ArrowHandler";
import { SortableDragHandler } from "../components/Sortable"
import CustomInput from "./CustomInput"
import Constrained from "./Constrained"
import "./Task.scss";
import LongClickInput from "./LongClickInput";
import { useState, useEffect } from "react";
import useNow from "../hooks/useNow";

const DAY = 60 * 60 * 24 * 1000

function RewardInput({ value, onChange, className, showRewardColumn }) {
  const [isEditing, setIsEditing] = useState(false);
  const [localValue, setLocalValue] = useState(value === null ? '' : (value === 0 ? '0' : String(value)));

  useEffect(() => {
    if (!isEditing) {
      setLocalValue(value === null ? '' : (value === 0 ? '0' : String(value)));
    }
  }, [value, isEditing]);

  const displayValue = showRewardColumn && (value === null || value === 0) 
    ? '' 
    : (value === null ? 'auto' : (value === 0 ? '0' : (value > 0 ? `+${value}` : `${value}`)));

  const handleClick = () => {
    setIsEditing(true);
    setLocalValue(value === null ? '' : (value === 0 ? '0' : String(value)));
  };

  const handleBlur = (e) => {
    setIsEditing(false);
    const inputValue = e && e.target ? e.target.value : localValue;
    let numValue = null;
    if (inputValue === '' || inputValue === null || inputValue === undefined) {
      numValue = null; //   = null (auto)
    } else if (inputValue === '-') {
      numValue = null; //    = null
    } else {
      const parsed = parseInt(inputValue, 10);
      if (isNaN(parsed)) {
        numValue = null;
      } else {
        numValue = parsed; // 0  0,     
      }
    }
    onChange(numValue);
  };

  const handleChange = (e) => {
    setLocalValue(e.target.value);
  };

  const handleKeyDown = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      if (e.target) {
        e.target.blur();
      } else {
        handleBlur();
      }
    }
  };

  return (
    <div className={`reward-input ${className}`}>
      {isEditing ? (
        <input
          type="number"
          autoFocus
          value={localValue}
          onChange={handleChange}
          onBlur={handleBlur}
          onKeyDown={handleKeyDown}
          placeholder="auto"
        />
      ) : (
        <div onClick={handleClick}>
          {displayValue}
        </div>
      )}
    </div>
  );
}

function TimeInput({ value, onChange }) {
  const [isEditing, setIsEditing] = useState(false);
  const [localValue, setLocalValue] = useState(value || '');

  useEffect(() => {
    if (!isEditing) {
      setLocalValue(value || '');
    }
  }, [value, isEditing]);

  const handleClick = () => {
    setIsEditing(true);
    setLocalValue(value || '');
  };

  // Normalize time input: convert "2000" to "20:00", or validate "20:00" format
  const normalizeTime = (input) => {
    if (!input || input === '' || input === null || input === undefined) {
      return '';
    }
    
    // Remove any whitespace
    const trimmed = input.trim();
    
    // If already in HH:MM format, validate it
    const timeRegex = /^([0-1]?[0-9]|2[0-3]):([0-5][0-9])$/;
    if (timeRegex.test(trimmed)) {
      return trimmed;
    }
    
    // Try to parse as 4-digit number (e.g., "2000" -> "20:00")
    const digitsOnly = trimmed.replace(/[^0-9]/g, '');
    if (digitsOnly.length === 4) {
      const hours = parseInt(digitsOnly.substring(0, 2), 10);
      const minutes = parseInt(digitsOnly.substring(2, 4), 10);
      if (hours >= 0 && hours <= 23 && minutes >= 0 && minutes <= 59) {
        return `${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}`;
      }
    }
    
    // Try to parse as 3-digit number (e.g., "800" -> "08:00")
    if (digitsOnly.length === 3) {
      const hours = parseInt(digitsOnly.substring(0, 1), 10);
      const minutes = parseInt(digitsOnly.substring(1, 3), 10);
      if (hours >= 0 && hours <= 9 && minutes >= 0 && minutes <= 59) {
        return `${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}`;
      }
    }
    
    // Invalid format
    return null;
  };

  const handleBlur = (e) => {
    setIsEditing(false);
    const inputValue = e && e.target ? e.target.value : localValue;
    const normalized = normalizeTime(inputValue);
    if (normalized !== null) {
      onChange(normalized);
    } else {
      // If invalid, revert to previous value
      setLocalValue(value || '');
    }
  };

  const handleChange = (e) => {
    setLocalValue(e.target.value);
  };

  const handleKeyDown = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      if (e.target) {
        e.target.blur();
      } else {
        handleBlur(e);
      }
    }
  };

  return (
    <div className="time-input">
      {isEditing ? (
        <input
          type="text"
          placeholder="HH:MM or HHMM"
          autoFocus
          value={localValue}
          onChange={handleChange}
          onBlur={handleBlur}
          onKeyDown={handleKeyDown}
          maxLength={5}
        />
      ) : (
        <div onClick={handleClick}>
          {value || ''}
        </div>
      )}
    </div>
  );
}


export default function ({
  title,
  titleColumnStyle,
  measuresWidth,
  scrollLeft,
  onTitleChange,
  children,
  reward,
  onRewardChange,
  showRewardColumn,
  showTimeColumns = true,
  timeFrom,
  onTimeFromChange,
  timeTo,
  onTimeToChange,
  timeFromNextDay,
  timeToNextDay,
  isMarkedToday,
  maxDayTime,
  displayFixedRewardText,
  displayFixedRewardClassName,
  isUncollapsed,
  onToggleUncollapsed,
}) {
  console.debug("task redraw")

  const rewardValue = typeof reward === 'number' ? reward : null
  const rewardClassName = typeof reward === 'number' ? (reward > 0 ? 'reward-positive' : reward < 0 ? 'reward-negative' : 'reward-zero') : 'reward-auto'

  let badgeRewardText = rewardValue > 0 ? `+${rewardValue}` : rewardValue
  if (displayFixedRewardText) {
    badgeRewardText = displayFixedRewardText
  }

  //  
  const { now } = useNow(60000) //   
  const currentDate = new Date(now)
  const currentMinutes = currentDate.getHours() * 60 + currentDate.getMinutes()
  
  // ,      
  //     ( dd.mm.yyyy)
  const getDateOnly = (timestamp) => {
    const d = new Date(timestamp)
    return new Date(d.getFullYear(), d.getMonth(), d.getDate()).getTime()
  }
  const isDateExpired = maxDayTime && getDateOnly(now) > getDateOnly(maxDayTime)
  const isCurrentDayMaxDay = maxDayTime && getDateOnly(now) === getDateOnly(maxDayTime)
  const isNextDay = maxDayTime && getDateOnly(now) === getDateOnly(maxDayTime + DAY)

  let timeStatus = null // null, 'in-range', 'out-of-range'
  //          timeFrom  timeTo  
  //    ,    (  )
  if (timeFrom || timeTo) {
    const parseTime = (timeStr) => {
      if (!timeStr) return null
      const [hours, minutes] = timeStr.split(':').map(Number)
      return hours * 60 + minutes
    }

    let fromMinutes = parseTime(timeFrom)
    let toMinutes = parseTime(timeTo)
    
    //    maxDayTime (00:00  )
    const getDayStart = (timestamp) => {
      const d = new Date(timestamp)
      return new Date(d.getFullYear(), d.getMonth(), d.getDate()).getTime()
    }
    
    const maxDayStart = maxDayTime ? getDayStart(maxDayTime) : null
    const nextDayStart = maxDayTime ? getDayStart(maxDayTime + DAY) : null
    
    // ,     next day
    //  timeFromNextDay = true,      
    //  timeToNextDay = true,      
    const fromIsNextDay = timeFromNextDay && maxDayTime
    const toIsNextDay = timeToNextDay && maxDayTime
    
    //  timeFrom  ,  timeTo ,  00:00  maxDayTime (  ,   )
    if (fromMinutes === null && maxDayStart !== null) {
      fromMinutes = fromIsNextDay ? 1440 : 0 // 00:00    00:00  
    }
    
    //  timeTo  ,  timeFrom ,  00:00   (maxDayTime + DAY)
    //  ,  toMinutes  1440 (24:00 = 00:00  )
    //    next day,    00:00    (2880 )
    if (toMinutes === null && nextDayStart !== null) {
      toMinutes = toIsNextDay ? 2880 : 1440 // 00:00     00:00  
    }

    //    ,     
    if (isDateExpired) {
      timeStatus = 'out-of-range'
    } else if (fromMinutes !== null && toMinutes !== null) {
      //      next day
      //  fromIsNextDay = true,       
      //  toIsNextDay = true,       
      const shouldCheckFrom = !fromIsNextDay || isNextDay
      const shouldCheckTo = !toIsNextDay || isNextDay
      
      //     (timeFrom  timeTo),   (    next day)
      //      (  ,   maxDayTime),       
      const hasExplicitValues = timeFrom || timeTo
      const shouldCheckTime = !maxDayTime || isCurrentDayMaxDay || isNextDay || hasExplicitValues
      
      if (shouldCheckTime && shouldCheckFrom && shouldCheckTo) {
        //      ,   currentMinutes
        // currentMinutes      (now)
        //      (maxDayTime + DAY),  currentMinutes  
        //     maxDayTime,      ,    
        
        let adjustedFromMinutes = fromMinutes
        let adjustedToMinutes = toMinutes
        
        //  fromIsNextDay = true     maxDayTime,     
        if (fromIsNextDay && isCurrentDayMaxDay) {
          adjustedFromMinutes = Infinity //      
        }
        //  toIsNextDay = true     maxDayTime,     
        if (toIsNextDay && isCurrentDayMaxDay) {
          adjustedToMinutes = -1 //      
        }
        
        //        ( ,   )
        if (adjustedFromMinutes <= adjustedToMinutes) {
          //   (, 09:00 - 17:00  00:00 - 24:00)
          if (currentMinutes >= adjustedFromMinutes && currentMinutes <= adjustedToMinutes) {
            timeStatus = 'in-range'
          } else if (currentMinutes > adjustedToMinutes) {
            //  out-of-range      timeTo
            timeStatus = 'out-of-range'
          }
          //  currentMinutes < adjustedFromMinutes, timeStatus  null ( )
        } else {
          //    (, 22:00 - 04:00)
          if (currentMinutes >= adjustedFromMinutes || currentMinutes <= adjustedToMinutes) {
            timeStatus = 'in-range'
          } else if (currentMinutes > adjustedToMinutes && currentMinutes < adjustedFromMinutes) {
            //  out-of-range      timeTo (     fromMinutes)
            timeStatus = 'out-of-range'
          }
        }
      }
    } else if (fromMinutes !== null) {
      //  timeFrom  (timeTo    maxDayTime  )
      //     next day
      const shouldCheckFrom = !fromIsNextDay || isNextDay
      if (shouldCheckFrom) {
        let adjustedFromMinutes = fromMinutes
        if (fromIsNextDay && isCurrentDayMaxDay) {
          adjustedFromMinutes = Infinity
        }
        if (currentMinutes >= adjustedFromMinutes) {
          timeStatus = 'in-range'
        }
      }
    } else if (toMinutes !== null) {
      //  timeTo  (timeFrom    maxDayTime  )
      //     next day
      const shouldCheckTo = !toIsNextDay || isNextDay
      if (shouldCheckTo) {
        let adjustedToMinutes = toMinutes
        if (toIsNextDay && isCurrentDayMaxDay) {
          adjustedToMinutes = -1
        }
        if (currentMinutes > adjustedToMinutes) {
          timeStatus = 'out-of-range'
        }
      }
    }
  }

  //          
  //   ( ,  0, NA),   
  const taskRowClassName = !isMarkedToday && timeStatus === 'out-of-range' ? 'task-time-out-of-range' : 
                           (!isMarkedToday && timeStatus === 'in-range') ? 'task-time-in-range' : ''

  return (
    <div className={`task-row ${taskRowClassName}`}>
      <div className="task-title-column task-swipe-handler" style={titleColumnStyle}>
        <SortableDragHandler>
          <ArrowHandler 
            collapsed={!isUncollapsed} 
            onClick={onToggleUncollapsed}
          />
        </SortableDragHandler>
        <div className="title">
          {onTitleChange ? (
            <LongClickInput value={title} onChange={value => onTitleChange(value)} />
          ) : (
            <div className="title-static">{title}</div>
          )}
        </div>
        <div className="compact-badges">
          {!showTimeColumns && (timeFrom || timeTo) && (
            <div className="badge badge-time">
              {timeFrom && timeTo ? `${timeFrom}-${timeTo}` : timeFrom || timeTo}
            </div>
          )}
          {!showRewardColumn && (rewardValue !== 0 && rewardValue !== null || displayFixedRewardText) && (
            <div className={`badge badge-reward ${displayFixedRewardClassName}`}>
              {badgeRewardText}
            </div>
          )}
        </div>
      </div>
      {showRewardColumn && (
        <div className="reward-column cell">
          {onRewardChange ? (
            <RewardInput
              value={rewardValue}
              onChange={value => onRewardChange(value)}
              className={rewardClassName}
              showRewardColumn={showRewardColumn}
            />
          ) : (
            <div className={`reward-input ${rewardClassName}`}>
              <div>
                {showRewardColumn 
                  ? ((reward === null || reward === undefined || rewardValue === 0) ? '' : (rewardValue > 0 ? `+${rewardValue}` : `${rewardValue}`))
                  : (rewardValue === null ? 'auto' : (rewardValue === 0 ? '0' : (rewardValue > 0 ? `+${rewardValue}` : `${rewardValue}`)))
                }
              </div>
            </div>
          )}
        </div>
      )}
      {showTimeColumns && (
        <>
          <div className="time-column cell">
            {onTimeFromChange ? (
              <TimeInput
                value={timeFrom}
                onChange={value => onTimeFromChange(value)}
              />
            ) : (
              <div className="time-input">
                <div>{timeFrom || ''}</div>
              </div>
            )}
          </div>
          <div className="time-column cell">
            {onTimeToChange ? (
              <TimeInput
                value={timeTo}
                onChange={value => onTimeToChange(value)}
              />
            ) : (
              <div className="time-input">
                <div>{timeTo || ''}</div>
              </div>
            )}
          </div>
        </>
      )}
      <Constrained width={measuresWidth} scrollLeft={scrollLeft}>
        <div className="measures">
          {children}
        </div>
      </Constrained>
    </div>
  )
}