import { useRef } from "react"
import "./ArrowHandler.scss"

export default function ArrowHandler({ collapsed = false, onClick }) {
  const pointerDownRef = useRef(null)

  const handlePointerDown = (e) => {
    pointerDownRef.current = { x: e.clientX, y: e.clientY }
  }

  const handlePointerUp = (e) => {
    if (!pointerDownRef.current) return
    
    const deltaX = Math.abs(e.clientX - pointerDownRef.current.x)
    const deltaY = Math.abs(e.clientY - pointerDownRef.current.y)
    
    //    5px,   ,   drag
    if (deltaX < 5 && deltaY < 5 && onClick) {
      e.stopPropagation()
      onClick(e)
    }
    
    pointerDownRef.current = null
  }

  return (
    <button 
      className="arrow-handler-button" 
      onPointerDown={handlePointerDown}
      onPointerUp={handlePointerUp}
    >
      <div className="arrow-handler">
        <div className={`arrow ${collapsed ? 'arrow-right' : 'arrow-down'}`}></div>
      </div>
    </button>
  )
}

