import { MEASURE_TYPE_EMPTY, MEASURE_TYPE_NUMBER } from "../models/Measure"
import { useMeasures } from "../store/measures"
import { useDays } from "./days"
import { useTasks } from "./tasks"
import { getFreq, total } from "../utils/taskUtils"

const formatPercent = x => x == /** approx */ 0 ? 0 : Math.floor(x * 100) + "%";

export function useStats() {
  const days = useDays()
  const tasks = useTasks()
  const { getMeasureValueByTaskIdAndDayId } = useMeasures()

  return {
    total(taskId) {
      return total(taskId, tasks, days, getMeasureValueByTaskIdAndDayId)
    },
    freq28(taskId) {
      return formatPercent(getFreq(taskId, 28, tasks, days, getMeasureValueByTaskIdAndDayId))
    },
    freq7(taskId) {
      return formatPercent(getFreq(taskId, 7, tasks, days, getMeasureValueByTaskIdAndDayId))
    },
  }

}  